.newsletter {
  position: relative;
  overflow: hidden;
  border-radius: var(--gsc-base-radii, 0);
  z-index: 0;
}

.newsletter__container::before {
  content: '';
  padding-bottom: var(--gsc-section-ratio);
}

.newsletter__container {
  display: flex;
}

.newsletter__container--mobile-content-with-background
  .newsletter__content-wrap--mobile {
  padding: 1.2rem;
}

.newsletter__content {
  position: relative;
  z-index: 1;
  width: 100%;
  max-width: var(--gsc-content-width);
  padding: 2rem;
  text-align: var(--gsc-content-align);
  border-radius: var(--gsc-base-radii, 0);
}

.newsletter__content-wrap--desktop {
  display: none;
}

.newsletter__content--with-solid-background {
  background-color: var(--gsc-background-color-100);
}

.newsletter__content--with-transparent-background {
  background-color: var(--gsc-background-color-15);
  backdrop-filter: blur(16.5px);
}

.newsletter__content--with-new-transparent-background {
  background-color: var(--gsc-background-color-30);
}

.newsletter__content--with-outline-background {
  border: 2px solid var(--gsc-text-color-100);
}

.newsletter__content--with-shadow-background {
  position: relative;
}

.newsletter__content--with-shadow-background::before {
  @include box(calc(100% + 4rem), calc(100% + 4rem));

  position: absolute;
  top: 50%;
  left: 50%;
  z-index: -1;
  background: radial-gradient(
    50% 50% at 50% 50%,
    var(--gsc-black-color-30) 0%,
    var(--gsc-black-color-10) 100%
  );
  transform: translate(-50%, -50%);
  filter: blur(100px);
  content: '';
}

.newsletter__media {
  position: absolute;
  inset: 0;
  z-index: 0;
}

.newsletter__input-btn {
  flex: 1;
  min-width: fit-content;
  padding-right: 2.4rem;
  padding-left: 2.4rem;
}

.newsletter__field {
  display: flex;
  flex-wrap: wrap;
  row-gap: 1.6rem;
}

.newsletter__input {
  padding: 1.6rem 2.4rem;
}

.newsletter__text,
.newsletter__heading {
  margin: 1.2rem 0;
}

.newsletter__form,
.newsletter__timer {
  margin: 2.4rem 0;
}

.newsletter__content > *:first-child {
  margin-top: 0;
}

.newsletter__content > *:last-child {
  margin-bottom: 0;
}

.newsletter__form {
  width: 100%;
}

.newsletter__arrow {
  @include box(2.4rem, 2.4rem);
}

@media (--md-screen) {
  .newsletter__text,
  .newsletter__caption,
  .newsletter__heading {
    margin: 1.6rem 0;
  }

  .newsletter__form,
  .newsletter__timer {
    margin: 3.2rem 0;
  }

  .newsletter__input {
    flex: 1 1 29.5rem;
    padding: 1.3rem 1.6rem;
  }

  .newsletter__content {
    padding: 2.8rem;
  }

  .newsletter__content-wrap--mobile {
    display: none;
  }

  .newsletter__content-wrap--desktop {
    display: block;
  }

  .newsletter__content-wrap {
    padding: var(--gsc-content-spacing);
  }

  .newsletter__field {
    display: inline-flex;
    flex-wrap: wrap;
    row-gap: 1.6rem;
    max-width: 45.2rem;
  }

  .round-style .newsletter__field {
    column-gap: 1.6rem;
  }
}

@media (--lg-screen) {
  .newsletter__content {
    padding: 3.2rem;
  }
}

@media (--xl-screen) {
  .newsletter__content {
    padding: 4rem;
  }
}

@media (--xxl-screen) {
  .newsletter__content {
    padding: 4.4rem;
  }
}
