.product-content-tab__title {
  display: flex;
  align-items: center;
  margin: 0 0 2rem;
  column-gap: 0.8rem;
}

.product-content-tab__title-icon {
  @include box(var(--gsc-icon-size, 2.4rem), var(--gsc-icon-size, 2.4rem));
  object-fit: contain;
}

.product-content-tab__content {
  display: flex;
  flex-flow: column;
  row-gap: 2.4rem;

  @include font(var(--gsc-fz-12), var(--gsc-lh-5));
}

.product-content-tab__image {
  @include box(100%, auto);
  background: var(--gsc-foreground-color-100);
}

.product-content-tab__rte ul,
.product-content-tab__rte ol {
  padding: 0 0 0 2rem;
  row-gap: 0.4rem;
}

.product-content-tab__btn {
  display: flex;
  align-items: center;
  padding: 2rem 0;
  column-gap: 0.8rem;
  cursor: pointer;
}

@media (--md-screen) {
  .product-content-tab__image {
    width: var(--gsc-image-width);
  }
}
