@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }

  70% {
    transform: scale(1.55);
    opacity: 0.1;
  }

  100% {
    transform: scale(1);
    opacity: 0.1;
  }
}

.product-stock {
  display: flex;
  align-items: center;
  padding: 0.4rem 0;
}

.product-stock__label {
  margin: 0 0 0 0.4rem;
  @include font(var(--gsc-fz-12), var(--gsc-lh-5), var(--gsc-font-weight-bold));
}

.product-stock__label--colored {
  color: var(--gsc-product-stock-color);
}

.product-stock__icon-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  @include box(2.4rem, 2.4rem);
  margin: 0 0 0 -0.6rem;
  overflow: visible;
}

.product-stock__icon {
  position: relative;
  @include box(1.2rem, 1.2rem);
  background-color: var(--gsc-product-stock-color);
  border-radius: 50%;
}

.product-stock__icon::before {
  position: absolute;
  top: 0;
  left: 0;
  @include box(100%, 100%);
  background-color: var(--gsc-product-stock-color);
  border-radius: 50%;
  content: '';
}

.product-stock--high {
  --gsc-product-stock-color: var(
    --gsc-product-high-level-inventory-indicator-color
  );
}

.product-stock--low {
  --gsc-product-stock-color: var(
    --gsc-product-low-level-inventory-indicator-color
  );
}

.product-stock--zero {
  --gsc-product-stock-color: var(--gsc-text-color-100);
}

.product-stock--high .product-stock__icon::before,
.product-stock--low .product-stock__icon::before {
  animation: pulse 1s ease-in-out infinite;
}

.product-stock--zero {
  opacity: 0.35;
}
