.search__form {
  margin: 0 0 4rem;
}

.search__no-results-note {
  font-weight: var(--gsc-font-weight-medium);
  text-align: center;
}

.search__input {
  height: 5.6rem;
  padding-right: 5.6rem;
}

.search__input-btn {
  width: 5.4rem;
  color: var(--gsc-text-color-70);
  background: transparent;
  border: none;
}

.search__input-btn-icon {
  width: 2.4rem;
}

.search__tab-name-list {
  display: none;
  align-items: center;
  justify-content: center;
  margin: 0 0 6.4rem;
  padding: 0.3rem 0;
  column-gap: 3.2rem;
}

.search__carousel-tab-names {
  width: 100%;
  margin: 0 0 4rem;
  padding: 0.3rem 0;

  --gsc-space-between-cards: 3.2rem;
}

.search__carousel-tab-names-viewport {
  width: 100%;
}

.search__tab-name {
  min-width: fit-content;
  text-transform: none;

  @include font(var(--gsc-fz-12), var(--gsc-lh-5), var(--gsc-font-weight-bold));
}

.search__results-container {
  padding-top: 0;
}

@media (--md-screen) {
  .search__container {
    max-width: 73.6rem;
  }

  .search__carousel-tab-names {
    display: none;
  }

  .search__tab-name-list {
    display: flex;
  }

  .search__form {
    margin: 0 0 6.4rem;
  }
}
