.alert {
  display: flex;
  align-items: center;
  padding: 1.6rem 2.1rem;
  column-gap: 1.2rem;
  background: var(--gsc-white-color-100);
  border-radius: 15px;
}

.alert[alert-type='warning'] {
  color: var(--gsc-red-color-100);
}

.alert[alert-type='success'] {
  color: var(--gsc-green-color-100);
}

.alert__icon {
  flex-shrink: 0;
  width: 2.4rem;
}

.alert__close-icon {
  @include box(2rem, 2rem);
}

.alert__close-icon-wrapper {
  margin-left: auto;
  cursor: pointer;
  opacity: 0.15;
}

.alert__text {
  padding: 0.4rem 0 0;
  
  @include font(var(--gsc-fz-14), var(--gsc-lh-15), var(--gsc-font-weight-medium));
}

@media (--md-screen) {
  .alert__close-icon-wrapper:hover {
    opacity: 1;
  }
}
