.contact__content {
  margin: 0 0 4rem;
  text-align: var(--gsc-text-align);
}

.contact__content > * + * {
  margin: 2rem 0 0;
}

.contact__form {
  display: flex;
  flex-flow: column;
}

.contact__form > .contact__field + .contact__field,
.contact__field-group + .contact__field {
  margin: 1.6rem 0 0;
}

.contact__captcha-note,
.contact__btn {
  margin: 2.4rem 0 0;
}

.contact__captcha-note {
  color: var(--gsc-text-color-70);
  text-align: center;

  @include font(var(--gsc-fz-14), var(--gsc-lh-6));
}

@media (--md-screen) {
  .contact__content {
    margin: 0 0 4rem;
  }

  .contact__form > .contact__field + .contact__field,
  .contact__field-group + .contact__field {
    margin: 2.4rem 0 0;
  }

  .contact__captcha-note,
  .contact__btn {
    margin: 3.2rem auto 0;
  }
}
