.share {
  display: inline-block;
}

.share__btn {
  display: flex;
  align-items: center;
  column-gap: 0.8rem;
  cursor: pointer;
  background: transparent;
  border: none;
  color: var(--gsc-text-color-100);
  appearance: none;

  @include font(var(--gsc-fz-12), var(--gsc-lh-5));
}

.share__copied-status-tooltip {
  position: absolute;
}

.share-dropdown__item {
  display: flex;
  align-items: center;
  color: var(--gsc-text-color-70);
  column-gap: 1.2rem;
  cursor: pointer;

  @include font(
    var(--gsc-fz-12),
    var(--gsc-lh-5),
    var(--gsc-font-weight-medium)
  );
}

.share-dropdown__item-icon {
  @include box(2.2rem, 2.2rem);
}

.share-dropdown {
  z-index: var(--gsc-share-dropdown-zIndex);
}

.share-dropdown__list {
  display: flex;
  flex-flow: column;
  padding: 2.4rem 4rem 2.4rem 2.4rem;
  background-color: var(--gsc-background-color-100);
  box-shadow: 0 1.2rem 3.2rem rgb(20, 20, 22, 0.08);
  row-gap: 1.2rem;
}

.share-dropdown__item:active {
  color: var(--gsc-text-color-35);
}

.share-dropdown__item {
  text-decoration: none;
}

@media (--md-screen) {
  .share-dropdown__item:not(:active):hover {
    color: var(--gsc-text-color-100);
  }
}
