:root {
  /* image ratio */

  --gsc-square-image-ratio: calc((1 / 1) * 100%);
  --gsc-portrait-image-ratio: calc((4 / 3) * 100%);
  --gsc-large-portrait-image-ratio: calc((3 / 2) * 100%);
  --gsc-landscape-image-ratio: calc((3 / 4) * 100%);
  --gsc-narrow-landscape-image-ratio: calc((0.565 / 1) * 100%);

  /* icons */

  --gsc-filled-star-24: url('data:image/svg+xml;base64,');
  --gsc-half-filled-star-24: url('data:image/svg+xml;base64,');
  --gsc-half-filled-star-48: url('data:image/svg+xml;base64,');
  --gsc-filled-star-48: url('data:image/svg+xml;base64,');
  --gsc-filled-star-56: url('data:image/svg+xml;base64,');
  --gsc-half-filled-star-56: url('data:image/svg+xml;base64,');
}
