@keyframes tabFade {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.tabs {
  position: relative;
}

.tab {
  position: static;
  z-index: 1;
  display: block;
}

.tab[aria-hidden='false'] {
  animation: tabFade 0.5s linear forwards;
}

.tab[aria-hidden='true'] {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  pointer-events: none;
}

.tab-name {
  color: var(--gsc-text-color-35);
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;

  @include font(
    var(--gsc-fz-13),
    var(--gsc-lh-7),
    var(--gsc-font-weight-semibold)
  );
}

.tab-name.selected {
  color: var(--gsc-text-color-100);
}

@media (--md-screen) {
  .tab-name:hover {
    color: var(--gsc-text-color-100);
  }
}
