.spacer__content-wrapper {
  border-radius: var(--gsc-base-radii, 0);
}

.spacer__content {
  display: flex;
  align-items: center;
  height: var(--gsc-spacer-height);
}

.spacer__separator {
  @include box(100%, 1px);
  background: var(--gsc-border-color-100);
}

.spacer--desktop {
  display: none;
}

@media (--md-screen) {
  .spacer--desktop {
    display: flex;
  }

  .spacer--mobile {
    display: none;
  }
}
