.login__content {
  margin: 0 0 4rem;
  text-align: center;
}

.login__footer {
  margin: 4rem 0 0;
  color: var(--gsc-text-color-70);
  text-align: center;

  @include font(
    var(--gsc-fz-13),
    var(--gsc-lh-7),
    var(--gsc-font-weight-medium)
  );
}

.login__btns {
  display: flex;
  flex-flow: column;
  justify-content: center;
  margin: 4rem 0 0;
  column-gap: 2.4rem;
  row-gap: 1.6rem;
}

.login__btn {
  width: 100%;
}

.login__field {
  display: flex;
  flex-flow: column;
}

.login__field + .login__field {
  margin: 2.4rem 0 0;
}

.login__field-link {
  align-self: flex-end;
  margin: 1.6rem 0 0;
  text-decoration: underline;
}

.login__title {
  margin: 0 0 2rem;
}

.login__text {
  color: var(--gsc-text-color-80);

  @include font(var(--gsc-fz-11), var(--gsc-lh-4));
}

@media (--md-screen) {
  .login__content {
    margin: 0 0 4rem;
  }

  .login__footer {
    margin: 4.8rem 0 0;
  }

  .login__btns {
    flex-flow: row;
    margin: 4.8rem 0 0;
  }

  .login__btn {
    width: auto;
    min-width: 22rem;
  }

  #recover .login__btn {
    width: 100%;
  }
}
