.cart-items__header {
  display: flex;
  flex-flow: column;
  align-items: center;
  margin: 0 0 4rem;
}

.cart-items__header-product-count {
  margin: 1.6rem 0 0;
  color: var(--gsc-text-color-70);

  @include font(var(--gsc-fz-12), var(--gsc-lh-5));
}

.cart-items__params {
  display: none;
}

@media (--md-screen) {
  .cart-items__header {
    flex-flow: row;
    align-items: flex-end;
    justify-content: space-between;
    margin: 0 0 4rem;
  }

  .cart-items__header-product-count {
    margin: 0;
  }

  .cart-items__params {
    display: flex;
    padding: 0 0 1.6rem;
    border-bottom: 1px solid var(--gsc-border-color-100);
    column-gap: 2.4rem;

    @include font(
      var(--gsc-fz-12),
      var(--gsc-lh-5),
      var(--gsc-font-weight-bold)
    );
  }

  .cart-items__left-side-params {
    display: flex;
    flex: 1;
  }

  .cart-items__right-side-params {
    display: flex;
    flex: 1;
  }

  .cart-items__params-item {
    flex: 1;
  }

  .cart-items__params-item:not(:first-child) {
    padding-left: 2.4rem;
  }

  .cart-items__params-item--total-param {
    text-align: right;
  }

  .cart-items__params-item--quantity {
    max-width: 17.4rem;
  }
}
