.page-margin,
.shopify-challenge__container {
  margin: 7rem auto !important;
  text-align: center;
}

.hidden {
  display: none !important;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  height: 100%;
  color: var(--gsc-text-color-100);
  font-size: 62.5%;
}

.unstyled-button {
  padding: 0;
  overflow: visible;
  color: var(--gsc-text-color-100);
  font-size: inherit;
  line-height: inherit;
  background: none;
  border: none;
  border-radius: 0;
  cursor: pointer;
  appearance: none;
  touch-action: manipulation;
}

.unstyled-link {
  color: inherit;
  text-decoration: none;
}

*[tabindex='-1']:focus-visible {
  outline: none;
}

body {
  position: relative; /* for overlay */
  display: flex;
  flex-flow: column;
  min-height: 100vh;
  margin: 0;
  overflow-x: hidden;
  overflow-y: scroll;
  color: var(--gsc-text-color-100);
  font-family: var(--gsc-body-font-family);
  font-style: var(--gsc-body-font-style);
  letter-spacing: var(--gsc-body-letter-spacing);
  word-spacing: var(--gsc-body-word-spacing);
  background: var(--gsc-background-color-100);

  @include font(
    var(--gsc-fz-11),
    var(--gsc-lh-base),
    var(--gsc-body-font-weight)
  );
}

body:not(.using-keyboard) :focus {
  outline: none;
}

main {
  display: flex;
  flex: 1;
  flex-flow: column;
}

section {
  overflow: hidden;
}

.section--with-sticky-elements {
  overflow: unset;
}

svg {
  display: block;
  flex-shrink: 0;
}

svg * {
  vector-effect: non-scaling-stroke;
}

*:focus-visible {
  outline-offset: -1px;
}

p {
  margin: 0;
}

::selection {
}

img::selection {
  background: transparent;
}

.shopify-challenge__message {
  margin: 0 0 1.6rem;
}

.shopify-challenge__button {
  margin: 2.4rem auto 0 !important;
}

.rte {
  overflow: hidden;

  --gsc-paragraph-spacing: 2rem;
}

.rte img {
  display: block;
  margin: 2rem 0;
}

.rte ul,
.rte ol {
  display: flex;
  flex-flow: column;
  row-gap: 0.6rem;
}

.rte iframe {
  max-width: 100%;
}

table:not([class]) {
  border-collapse: collapse;
}

table:not([class]) td,
table:not([class]) th {
  padding: 1rem;
  border: 0.1rem solid var(--gsc-text-color-100);
}

.rte--blog-post p,
.rte--page p {
  line-height: var(--gsc-lh-1);
}

.rte table:not(.size-guide-table),
.rte iframe,
.rte video,
.rte img {
  max-width: 100%;
}

.rte table:not(.size-guide-table) {
  display: block;
  overflow: auto;
}

.rte div + div,
.rte p + p .rte div + div,
.rte p + p {
  margin-top: var(--gsc-paragraph-spacing);
}

.rte > *:first-child *:first-child,
.rte > *:first-child {
  margin-top: 0;
}

.rte > *:last-child *:last-child,
.rte > *:last-child {
  margin-bottom: 0;
}

.rte h1,
.rte h2,
.rte h3,
.rte h4,
.rte h5,
.rte h6 {
  margin-top: 4rem;
  margin-bottom: 1.6rem;
}

.rte h1:first-child,
.rte h2:first-child,
.rte h3:first-child,
.rte h4:first-child,
.rte h5:first-child,
.rte h6:first-child {
  margin-top: 0;
}

.rte h1:last-child,
.rte h2:last-child,
.rte h3:last-child,
.rte h4:last-child,
.rte h5:last-child,
.rte h6:last-child {
  margin-bottom: 0;
}

.size-guide-table {
  width: 100%;
  text-align: center;
  table-layout: fixed;
  border-collapse: collapse;

  --gsc-table-border-color: var(--gsc-border-color-100);
  --gsc-table-filled-cell-color: var(--gsc-foreground-color-100);
}

.size-guide-table td {
  border: 1px solid var(--gsc-table-border-color);
}

.size-guide-table tr,
.size-guide-table td {
  margin: 0;
}

.size-guide-table tbody td,
.size-guide-table thead td {
  width: auto !important;

  @include font(var(--gsc-fz-11), var(--gsc-lh-4));
}

.size-guide-table tbody td {
  padding: 1rem 0.8rem;
  font-weight: var(--gsc-font-weight-medium);
}

.size-guide-table thead tr,
.size-guide-table tbody tr:nth-child(4n + 3),
.rte tbody tr:nth-child(4n + 4) {
  background-color: var(--gsc-table-filled-cell-color);
}

.size-guide-table thead td {
  padding: 1.6rem 0.8rem;
  font-weight: var(--gsc-font-weight-bold);
  text-transform: uppercase;
}

.delivery-table {
  width: 100%;
  color: var(--gsc-text-color-100);
  text-align: center;
  border-collapse: collapse;
}

.delivery-table tr,
.delivery-table td {
  margin: 0;
  border: none;
}

.delivery-table tbody td {
  width: auto !important;
  padding: 2rem 0;
  text-align: left;

  @include font(
    var(--gsc-fz-12),
    var(--gsc-lh-5),
    var(--gsc-font-weight-medium)
  );
}

.delivery-table tbody td + td {
  font-weight: var(--gsc-font-weight-bold);
  text-align: right;
}

.delivery-table tr + tr {
  border-top: 1px solid var(--gsc-border-color-100);
}

