.mobile-body-color.rte,
.body-color.rte,
.mobile-body-color .rte,
.body-color .rte,
.mobile-body-color,
.body-color {
  color: rgba(var(--gsc-text-color), var(--gsc-body-font-opacity)) !important;
}

@media (--md-screen) {
  .desktop-body-color.rte,
  .mobile-body-color.rte,
  .desktop-body-color .rte,
  .mobile-body-color .rte,
  .desktop-body-color,
  .mobile-body-color {
    color: rgba(var(--gsc-text-color), var(--gsc-body-font-opacity)) !important;
  }
}

.body0 {
  @include font(var(--gsc-body-font-size-xxl), var(--gsc-body-line-height-xxl));

  --gsc-body-line-height: var(--gsc-body-line-height-xxl);
  --gsc-body-font-size: var(--gsc-body-font-size-xxl);
}

.body1 {
  @include font(var(--gsc-body-font-size-xl), var(--gsc-body-line-height-xl));

  --gsc-body-line-height: var(--gsc-body-line-height-xl);
  --gsc-body-font-size: var(--gsc-body-font-size-xl);
}

.body2 {
  @include font(var(--gsc-body-font-size-l), var(--gsc-body-line-height-l));

