.quote {
  position: relative;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  text-align: left;
  border-radius: var(--gsc-base-radii);
  width: 100%;
  overflow: hidden;

  height: 100%;
}

.quote--with-background {
  background-color: var(--gsc-foreground-color-100);
}

.quote--with-sidebar {
  cursor: pointer;
}

.quote--with-sidebar:focus-visible {
  outline-offset: -2px;
}

.quote__link {
  position: absolute;
  inset: 0;
  z-index: 1;
}

.quote__image {
  width: 100%;
  height: auto;
  aspect-ratio: 1/1;
  object-fit: cover;
  border-radius: var(--gsc-base-radii);
}

.quote--with-background .quote__image {
  border-radius: 0;
}

.quote .rating__stars {
  column-gap: 0.4rem;
}

.quote .rating__star {
  width: 2rem;
  height: 2rem;
}

.quote__product-title {
  font-size: var(--gsc-fz-12);
  line-height: var(--gsc-lh-5);
  font-weight: 400;
  line-height: 22px;
  opacity: 0.8;
  z-index: 2;

  -webkit-line-clamp: 1;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  overflow: hidden;

  text-align: var(--gsc-quote-alignment);
}

.quote__text {
  font-family: var(--gsc-headings-font-family);

  @include font(
    var(--gsc-fz-11),
    var(--gsc-lh-base),
    var(--gsc-font-weight-semibold)
  );

  text-align: var(--gsc-quote-alignment);
}

.quote__text p {
  display: inline;
}

.quote__text-wrap {
  position: relative;
  display: block;
  flex: 1;
  overflow: hidden;
  opacity: 0;

  max-height: 18rem;

  display: flex;
  flex-flow: column;
  justify-content: center;
}

.quote__text-height {
  height: 100%;
  display: flex;
  align-items: center;
}

.quote__content {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  gap: 1.6rem;
  width: 100%;

  padding: 2.4rem;

  align-items: var(--gsc-quote-alignment);
}

.quote__content--without-right-padding {
  padding: 1.6rem 0.8rem 1.6rem 0;
}

.quote__content--without-aside-paddings {
  padding: 1.6rem 0.8rem 1.6rem 0;
}

.slider-grid__m-grid .quote__content--without-aside-paddings {
  padding: 1.6rem 0;
}

.slider-grid__m-carousel .quote__content--without-aside-paddings,
.slider-grid__m-slideshow .quote__content--without-aside-paddings {
  padding: 1.6rem 0.8rem;
}

.quote__content[data-line-clamp-setter-is-ready] .quote__text-wrap {
  opacity: 1;
}

.quote__text--truncate {
  @include lineClamp(var(--gsc-max-lines));
}

.quote__content[with-font-italic] .quote__text--truncate {
  font-style: italic;
}

.quote__details {
  display: flex;
  align-items: center;
  column-gap: 1.2rem;
  row-gap: 1.2rem;
}

.quote__details-credentials {
  display: flex;
  flex-flow: column;

  font-size: var(--gsc-fz-12);
  line-height: var(--gsc-lh-5);
}

.quote__author-name {
  font-weight: var(--gsc-font-weight-bold);
}

.quote__author-details {
  opacity: var(--gsc-body-font-opacity);
}

.quote__avatar-image {
  @include box(5rem, 5rem);
  object-fit: cover;
  border-radius: 50%;
}

.quote .link {
  color: var(--gsc-text-color-80);
  transition: color 0.2s ease-in-out;
}

.quote .link__text::before {
  background: var(--gsc-text-color-100);
}

.quote .link:hover {
  color: var(--gsc-text-color-100);
}

/* ___________SIDEBAR______________________ */

.quote-sidebar {
  right: unset;
  left: 0;
  width: 100%;
}

.quote-sidebar__body {
  width: 100%;
  color: var(--gsc-text-color-100);
}

.quote-sidebar__body--with-top-padding {
  padding-top: 7rem;
}

.quote-sidebar .quote {
  flex-direction: column;
  overflow-y: auto;
  height: 100%;
}

.quote-sidebar .quote__image {
  width: 100%;
  height: 100%;

  flex: 1;
}

.round-style .quote-sidebar .quote__image {
  margin: 1.2rem;
  border-radius: var(--gsc-image-inside-block-radii, 0);
  width: auto;
  max-width: 100%;
}

.quote-sidebar .quote__content {
  height: auto;
  margin: auto 0;
  flex: 1;
}

.quote-sidebar .quote__text-wrap {
  flex: unset;
  margin: 0;
  opacity: 1;
  max-height: 100%;

  -ms-overflow-style: none;
  scrollbar-width: none;
  border-radius: 0;
}

.quote-sidebar__close-button-container {
  position: absolute;
  top: 2.4rem;
  right: 2.4rem;
  z-index: 1;
}

.quote-sidebar__quote--with-image > .quote__content {
  justify-content: flex-start;
}

/* ___________SIDEBAR______________________ */

.quote--vertical .quote__content {
  height: 100%;
}

.testimonials__sidebar-button {
  height: 100%;
}

@media (--md-screen) {
  .quote:not(.quote-sidebar__quote).quote--vertical {
    height: unset;
  }

  /* ___________SIDEBAR______________________ */
  .quote-sidebar {
    width: unset;
  }

  .quote-sidebar__body--with-top-padding {
    padding-top: unset;
  }

  .round-style .quote-sidebar {
    padding: var(--gsc-modals-and-header-paddings);
  }

  .quote-sidebar__close-button-container {
    display: none;
  }

  .round-style .quote-sidebar__body {
    border-radius: var(--gsc-sidebar-radii, 0);
  }

  .quote-sidebar__body {
    width: 48rem;
  }

  .quote-sidebar .quote__content {
    padding: 3.2rem 4.4rem;
    gap: 2.4rem;
    height: unset;
  }

  .quote-sidebar .quote__text-height {
    height: unset;
  }

  .quote-sidebar .quote__text {
    @include font(
      var(--gsc-fz-9),
      var(--gsc-lh-4),
      var(--gsc-font-weight-semibold)
    );
  }

  /* ___________SIDEBAR______________________ */

  .quote {
    flex-direction: row;
    max-height: var(--gsc-quote-height);
    border-radius: var(--gsc-base-radii);
  }

  .quote--vertical {
    flex-flow: column;
    justify-content: flex-start;
    max-height: unset;
  }

  .quote__content {
    padding: 3.2rem;
  }

  .slider-grid__d-grid .quote__content--without-aside-paddings {
    padding: 1.6rem 0.8rem 1.6rem 0;
  }

  .slider-grid__d-carousel .quote__content--without-aside-paddings,
  .slider-grid__d-slideshow .quote__content--without-aside-paddings {
    padding: 1.6rem 0.8rem 1.6rem 0;
  }

  .quote__image {
    width: var(--gsc-quote-image-width);
    height: var(--gsc-quote-height);
    object-fit: cover;
    border-radius: var(--gsc-base-radii);
  }

  .quote--vertical .quote__image {
    width: 100%;
    height: 100%;
    aspect-ratio: 1/1;
  }

  .quote__text--small {
    @include font(
      var(--gsc-fz-11),
      var(--gsc-lh-base),
      var(--gsc-font-weight-semibold)
    );
  }

  .quote__text--medium {
    @include font(
      var(--gsc-fz-10),
      var(--gsc-lh-2),
      var(--gsc-font-weight-semibold)
    );
  }

  .quote__text--large {
    @include font(
      var(--gsc-fz-9),
      var(--gsc-lh-4),
      var(--gsc-font-weight-semibold)
    );
  }

  .quote::-webkit-scrollbar {
    display: none;
  }

  .quote--with-product-link:hover .link__text::before {
    bottom: 0;
    left: 0;
  }
}
