.addresses__form {
  padding: 4.8rem 0 4rem;
  border-bottom: 1px solid var(--gsc-border-color-100);
}

.addresses__address {
  display: flex;
  flex-flow: column;
  padding: 3.2rem 0;
  border-bottom: 1px solid var(--gsc-border-color-100);
}

.addresses__form-btns {
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  justify-content: flex-start;
  margin: 3.2rem 0 0;
  column-gap: 1.6rem;
  row-gap: 1.6rem;
}

.addresses__form-field-group {
  column-gap: 2.4rem;
  row-gap: 2.4rem;
}

.addresses__form-field + .addresses__form-field {
  margin: 2.4rem 0 0;
}

.addresses__accordeon[data-aria-expanded='true'] {
  padding: 0;
}

.addresses__form-title {
  margin: 0 0 4rem;
  text-align: center;
}

.addresses__address-details {
  color: var(--gsc-text-color-80);

  @include font(
    var(--gsc-fz-11),
    var(--gsc-lh-4),
    var(--gsc-font-weight-medium)
  );
}

.addresses__form-checkbox {
  @include font(
    var(--gsc-fz-12),
    var(--gsc-lh-5),
    var(--gsc-font-weight-medium)
  );
}

.addresses__address-details-item + .addresses__address-details-item {
  margin: 1.6rem 0 0;
}

.addresses__address-btns {
  display: flex;
  align-items: center;
  margin: 4rem 0 0;
  column-gap: 1.6rem;
}

.addresses__address-btn {
  min-width: 12.8rem;
}

.addresses__default-label {
  margin: 0 0 2.4rem;
  font-weight: var(--gsc-font-weight-bold);
}

.addresses__header {
  padding-bottom: 4rem;
  text-align: center;
  border-bottom: 1px solid var(--gsc-border-color-100);
}

.addresses__address-country-dropdown {
  height: 30rem;
  overflow: auto;
}

@media (--md-screen) {
  .addresses__form-field + .addresses__form-field {
    margin: 3.2rem 0 0;
  }

  .addresses__form-btns {
    flex-flow: row;
    margin: 4.8rem 0 0;
  }

  .addresses__form-title {
    margin: 0 0 4rem;
    text-align: left;
  }

  .addresses__header {
    padding-bottom: 2.4rem;
    text-align: left;
  }

  .addresses__address,
  .addresses__form {
    padding: 4.8rem 0;
  }

  .addresses__address {
    flex-flow: row;
    align-items: center;
    justify-content: space-between;
  }

  .addresses__address-btns {
    margin: 0;
  }
}
