.sort-list--desktop {
  display: none;
}

.sort-list-sidebar__form-wrap {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
}

.sort-list-sidebar__form {
  display: flex;
  flex-flow: column;
  align-items: center;
  padding: 2rem;
  row-gap: 2.4rem;
}

.sort-list-sidebar__form-label {
  position: relative;
}

.sidebar-sort-list {
  width: 100%;
}

.sort-list-sidebar__body {
  display: flex;
  flex-flow: column;
  width: 100%;
}

.sort-list-sidebar__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.2rem 1.6rem 1.2rem 2.4rem;
  text-transform: uppercase;

  @include font(var(--gsc-fz-12), var(--gsc-lh-5), var(--gsc-font-weight-bold));
}

.sort-list-sidebar__check-icon {
  position: absolute;
  top: 50%;
  left: -3rem;
  @include box(2.4rem, 2.4rem);
  transform: translateY(-50%);
}

.sort-list__btn {
  display: flex;
  align-items: center;
  column-gap: 1.1rem;
  cursor: pointer;

  @include font(var(--gsc-fz-12), var(--gsc-lh-5), var(--gsc-font-weight-bold));
}

.sort-list__btn-icon {
  @include box(2.4rem, 2.4rem);
}

.sort-list__btn path {
  transform-origin: center center;
  transition: transform 250ms ease;
}

.sort-list__btn.is-opened path {
  transform: scale(1, -1);
}

@media (--md-screen) {
  .sort-list--desktop {
    display: block;
  }

  .sort-list--mobile {
    display: none;
  }

  .sort-list-dropdown {
    position: absolute;
    z-index: var(--gsc-sort-list-dropdown-zIndex);
    width: 28rem;
    padding: 1.6rem;
    background: var(--gsc-background-color-100);
    border-radius: var(--gsc-default-dropdown-radii, 0);
    box-shadow: 0 2.4rem 6rem var(--gsc-black-color-15);
    opacity: 1;
    transition: opacity 0.125s ease-in-out;
  }

  .sort-list-dropdown[aria-hidden='true'] {
    opacity: 0;
    pointer-events: none;
  }

  .sort-list-dropdown__item {
    display: block;
    padding: 0.8rem 1.6rem;
    border-radius: var(--gsc-default-dropdown-item-radii, 0);

    @include font(
      var(--gsc-fz-12),
      var(--gsc-lh-5),
      var(--gsc-font-weight-medium)
    );
  }

  .sort-list-dropdown__item:hover,
  .sort-list-dropdown__item.active {
    background: var(--gsc-foreground-color-100);
  }

  .sort-list-dropdown__active-sort-option {
    font-weight: var(--gsc-font-weight-medium);
  }
}
