.password-header {
  padding: 1.6rem var(--gsc-container-padding);
}

.password-header__message {
  max-width: 40rem;
  color: var(--gsc-text-color-80);
  text-align: center;
}

.password-header__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: space-between;
  column-gap: 1.6rem;
  row-gap: 1.6rem;
}

.password-header__link {
  display: flex;
  align-items: center;
  color: var(--gsc-text-color-100);
  cursor: pointer;

  @include font(var(--gsc-fz-14), var(--gsc-lh-6));
}

.password-header__link span {
  text-decoration: underline;
}

.password-header__link-icon {
  width: 2.4rem;
  margin: 0 0.4rem 0 0;
}

@media (--md-screen) {
  .password-header__link-icon {
    margin: 0 0.8rem 0 0;
  }

  .password-header {
    padding: 2rem var(--gsc-container-padding);
  }

  .password-header__container {
    flex-flow: row;
  }

  .password-header__link {
    @include font(var(--gsc-fz-12), var(--gsc-lh-5));
  }
}
