.rich-text {
  display: flex;
}

.rich-text__container {
  width: 100%;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.rich-text__container--with-aside-spacing {
  padding: 2rem;
}

.round-style .rich-text__container {
  overflow: hidden;
  border-radius: var(--gsc-base-radii, 0);
}

.rich-text--block {
  align-items: center;
  padding: 2rem;
  overflow: hidden;
  background-color: var(--gsc-foreground-color-100);
  border-radius: var(--gsc-base-radii, 0);
}

.rich-text__truncate-text-wrapper {
  overflow: hidden;
}

.rich-text__content {
  max-height: 100%;
  margin: 0 auto;
}

.rich-text__content--with-left-align {
  align-items: flex-start;
  text-align: left;
}

.rich-text__content--with-left-align .rich-text__btns,
.rich-text__content--with-left-align .rich-text__timer-wrapper {
  justify-content: flex-start;
}

.rich-text__content--with-center-align {
  align-items: center;
  text-align: center;
}

.rich-text__content--with-center-align .rich-text__btns,
.rich-text__content--with-center-align .rich-text__timer-wrapper {
  justify-content: center;
}

.rich-text__content--with-right-align {
  align-items: flex-end;
  text-align: right;
}

.rich-text__content--with-right-align .rich-text__btns,
.rich-text__content--with-right-align .rich-text__timer-wrapper {
  justify-content: flex-end;
}

.rich-text__text-wrapper {
  display: flex;
  flex-flow: column;
  row-gap: 4.8rem;
  column-gap: 4.8rem;
}

.rich-text-section__text {
  flex: 1;
}

.rich-text__text--truncate {
  @include lineClamp(1);
}

.rich-text--block {
  opacity: 0;
}

.rich-text--block[data-line-clamp-setter-is-ready] {
  opacity: 1;
}

.rich-text__icon {
  display: inline-block;
  @include box(var(--gsc-section-icon-size), auto);
  object-fit: contain;
}

.rich-text__btns {
  display: flex;
  flex-wrap: wrap;
  column-gap: 1.6rem;
  row-gap: 1.6rem;
}

.rich-text__timer {
  width: var(--gsc-timer-block-width, 100%);
}

.rich-text__timer-wrapper {
  display: flex;
}

.rich-text__btn {
  align-self: flex-start;
  width: 100%;
}

.rich-text__caption,
.rich-text__heading,
.rich-text__text-wrapper {
  margin: 1.6rem 0;
}

.rich-text__btns,
.rich-text__timer-wrapper {
  margin: 3.2rem 0;
}

.rich-text__content > *:first-child {
  margin-top: 0;
}

.rich-text__content > *:last-child {
  margin-bottom: 0;
}

.rich-text__heading + .rich-text__text-wrapper {
  margin-top: 2.8rem;
}

.rich-text__content > *:first-child {
  margin-top: 0;
}

.rich-text__content > *:last-child {
  margin-bottom: 0;
}

@media (--md-screen) {
  .rich-text__container {
    padding-top: 2.8rem;
    padding-bottom: 2.8rem;
  }

  .rich-text__container--with-aside-spacing {
    padding: 2.8rem;
  }

  .rich-text__btn {
    width: auto;
  }

  .rich-text--block {
    padding: 2.8rem;
  }

  .rich-text__text-wrapper {
    flex-flow: row;
  }

  .rich-text__text-wrapper--with-large-desktop-gap {
    row-gap: 6.4rem;
    column-gap: 6.4rem;
  }
}

@media (--lg-screen) {
  .rich-text--block {
    padding: 3.2rem;
  }

  .rich-text__container {
    padding-top: 3.2rem;
    padding-bottom: 3.2rem;
  }

  .rich-text__container--with-aside-spacing {
    padding: 3.2rem;
  }
}

@media (--xl-screen) {
  .rich-text__container {
    padding-top: 4rem;
    padding-bottom: 4rem;
  }

  .rich-text__container--with-aside-spacing {
    padding: 4rem;
  }
}

@media (--xxl-screen) {
  .rich-text__container {
    padding-top: 4.4rem;
    padding-bottom: 4.4rem;
  }

  .rich-text__container--with-aside-spacing {
    padding: 4.4rem;
  }
}
