.cart-buttons {
  display: flex;
  align-items: center;
  flex-flow: column;
  column-gap: 1.2rem;
  row-gap: 1.2rem;
}

.cart-buttons__btn {
  width: 100%;
  padding: 1.3rem;
}

@media (--md-screen) {
  .cart-buttons__btn {
    padding: 1.7rem;
  }

  .cart-buttons {
    max-width: 42.4rem;
    width: 100%;
    margin-left: auto;
  }
}
