.tooltip {
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--gsc-tooltip-zIndex);
  width: max-content;
  display: none;
  filter: drop-shadow(0 8px 20px rgb(20, 20, 22, 0.1));
}

.tooltip__body {
  display: block;
  padding: 0.6rem 1.2rem;
  color: var(--gsc-background-color-100);
  background-color: var(--gsc-text-color-100);
  border-radius: 8px;

  @include font(var(--gsc-fz-13), var(--gsc-lh-7), var(--gsc-font-weight-bold));
}

.tooltip__arrow {
  position: absolute;
  z-index: 1;
  @include box(0.9rem, 0.9rem);
  background-color: var(--gsc-text-color-100);
  border: solid transparent;
  transform: rotate(45deg);
}

@media (--md-screen) {
  .tooltip {
    display: block;
  }

  .tooltip[aria-hidden='true'] {
    display: none;
    pointer-events: none;
  }

  .tooltip[aria-hidden='true'] * {
    pointer-events: none;
  }
}
