.modal {
  position: fixed;
  z-index: var(--gsc-modal-zIndex);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  transform: translateY(-100%);
  opacity: 0;
  transition-timing-function: ease;
  transition-duration: 0.25s;
  transition-property: opacity, transform;
  pointer-events: none;
  will-change: transform;
  inset: 0;
}

.modal.is-opened {
  transform: translateY(0%);
  opacity: 1;
  pointer-events: all;
}

.modal__body {
  position: relative;
  @include box(80%, 80%);
  margin: 0 auto;
  padding: 4rem;
  overflow: auto;
  overflow-x: hidden;
  overflow-y: auto;
  background-color: var(--gsc-background-color-100);
  outline: none;
}

.modal__close {
  position: absolute;
  top: 2rem;
  right: 2rem;
  z-index: 2;
  width: 2.4rem;
  margin: 0 0 0 auto;
  padding: 0;
  color: var(--gsc-text-color-100);
  background: transparent;
  border: none;
  cursor: pointer;
}
