.loading-overlay {
  position: fixed;
  inset: 0;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--gsc-text-color-100);
  background: var(--gsc-background-color-70);
  transition: opacity 0.4s ease-in-out;
}

.loading-overlay--global {
  background: var(--gsc-background-color-95);
  opacity: 0;
}

@keyframes loadingOverlayFadeIn {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes loadingOverlayHide {
  from {
    width: 100%;
    height: 100%;
  }

  to {
    width: 0;
    height: 0;
  }
}

.initial-page-loading-overlay {
  position: fixed;
  inset: 0;
  z-index: 9999;
  opacity: 1;
  background: var(--gsc-background-color-100);
  animation: loadingOverlayFadeIn 0.5s linear forwards,
    loadingOverlayHide 0s linear forwards 0.5s;
}

.loading-spinner {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  border: 2px solid var(--gsc-spinner-color, var(--gsc-text-color-100));
  animation: spinner-bulqg1 0.7s infinite linear alternate,
    spinner-oaa3wk 1.4s infinite linear;
}

@keyframes spinner-bulqg1 {
  0% {
    clip-path: polygon(50% 50%, 0 0, 50% 0%, 50% 0%, 50% 0%, 50% 0%, 50% 0%);
  }

  12.5% {
    clip-path: polygon(
      50% 50%,
      0 0,
      50% 0%,
      100% 0%,
      100% 0%,
      100% 0%,
      100% 0%
    );
  }

  25% {
    clip-path: polygon(
      50% 50%,
      0 0,
      50% 0%,
      100% 0%,
      100% 100%,
      100% 100%,
      100% 100%
    );
  }

  50% {
    clip-path: polygon(
      50% 50%,
      0 0,
      50% 0%,
      100% 0%,
      100% 100%,
      50% 100%,
      0% 100%
    );
  }

  62.5% {
    clip-path: polygon(
      50% 50%,
      100% 0,
      100% 0%,
      100% 0%,
      100% 100%,
      50% 100%,
      0% 100%
    );
  }

  75% {
    clip-path: polygon(
      50% 50%,
      100% 100%,
      100% 100%,
      100% 100%,
      100% 100%,
      50% 100%,
      0% 100%
    );
  }

  100% {
    clip-path: polygon(
      50% 50%,
      50% 100%,
      50% 100%,
      50% 100%,
      50% 100%,
      50% 100%,
      0% 100%
    );
  }
}

@keyframes spinner-oaa3wk {
  0% {
    transform: scaleY(1) rotate(0deg);
    opacity: 0;
  }

  49.99% {
    transform: scaleY(1) rotate(135deg);
    opacity: 1;
  }

  50% {
    transform: scaleY(-1) rotate(0deg);
    opacity: 1;
  }

  100% {
    transform: scaleY(-1) rotate(-135deg);
    opacity: 0;
  }
}
