.page-card {
  position: relative;
}

.page-card__link {
  z-index: 1;
}

.page-card__info {
  padding: 1.2rem 1.6rem 2rem 0;
}

.page-card__title {
  @include font(var(--gsc-fz-12), var(--gsc-lh-5), var(--gsc-font-weight-bold));
}

.page-card__icon {
  @include box(5.6rem, 5.4rem);
}

.page-card__icon--large {
  @include box(9.1rem, 8.8rem);
}

.page-card__link {
  position: absolute;
  top: 0;
  left: 0;
  @include box(100%, 100%);
}

.page-card__inner {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--gsc-foreground-color-100);
  border-radius: var(--gsc-base-radii);
}
