.dots-spinner {
  display: flex;
  align-items: center;
  column-gap: calc(var(--gsc-dot-size) * 2);

  --gsc-dot-size: 0.5rem;
  --gsc-dot-color: var(--gsc-text-color-100);
}

.dots-spinner__dot {
  @include box(var(--gsc-dot-size), var(--gsc-dot-size));
  background-color: var(--gsc-dot-color);
  border-radius: 50%;
}

.dots-spinner__dot {
  animation: dotPulse 1s infinite linear;
}

.dots-spinner__dot:nth-child(1) {
  animation-delay: 0s;
}

.dots-spinner__dot:nth-child(2) {
  animation-delay: 0.25s;
}

.dots-spinner__dot:nth-child(3) {
  animation-delay: 0.5s;
}

@keyframes dotPulse {
  0% {
    transform: scale(0);
  }

  50% {
    transform: scale(2);
  }

  100% {
    transform: scale(0);
  }
}
