.account__container {
  display: flex;
  flex-flow: column;
}

.account__content {
  flex: 1;
}

.account__add-address-btn {
  margin: 4rem 0 0;
}

.account__field + .account__field {
  margin: 3.2rem 0;
}

.account-menu__title {
  margin: 0 0 2.4rem;
  font-weight: var(--gsc-font-weight-bold);
  text-transform: uppercase;
}

.account-menu {
  display: none;
  flex-flow: column;
  min-width: 24.8rem;
}

.account-menu__item {
  display: flex;
  align-items: center;
  padding: 1.4rem 1.6rem;
  text-decoration: none;
  column-gap: 1.6rem;
  background-color: var(--gsc-foreground-color-100);
  border-radius: var(--gsc-base-radii, 0);
  cursor: pointer;

  @include font(var(--gsc-fz-12), var(--gsc-lh-5), var(--gsc-font-weight-medium));
}

.account-menu__item-chevron {
  @include box(2.2rem, 2.2rem);
  transform: rotate(90deg);
}

.account__header {
  padding: 0 0 4rem;
  text-align: center;
  border-bottom: 1px solid var(--gsc-border-color-100);
}

.account-menu__icon {
  @include box(2rem, 2rem);
}

@media (--md-screen) {
  .account-menu__item.active,
  .account-menu__item:hover {
    background: linear-gradient(
        0deg,
        var(--gsc-text-color-10),
        var(--gsc-text-color-10)
      ),
      var(--gsc-foreground-color-100);
  }

  .account-menu__item + .account-menu__item {
    margin: 1.6rem 0 0;
  }

  .account__header {
    text-align: left;
    border: none;
  }

  .account-menu {
    display: flex;
  }

  .account__dropdown-wrapper {
    display: none;
  }

  .account__container {
    flex-flow: row;
    column-gap: 8.2rem;
  }
}
