.product-price-wrapper {
  display: block;
}

.product-price__tax-status {
  margin: 0.4rem 0 0;

  @include font(var(--gsc-fz-14), var(--gsc-lh-6));
}

.product-price__content {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 1.6rem;
}

.product-price__cost {
  display: flex;
  align-items: baseline;
  column-gap: 0.6rem;
}

.product-price__current-price {
  color: var(--gsc-text-color-100);

  @include font(
    var(--gsc-product-price-fz),
    var(--gsc-product-price-lh),
    var(--gsc-product-price-fw)
  );
}

.product-price__current-price--highlight-sale {
  color: var(--gsc-product-highlight-price-color);
}

.product-price__old-price {
  color: var(--gsc-text-color-35);
  text-decoration: line-through;

  @include font(
    var(--gsc-product-old-price-font-size),
    var(--gsc-product-old-price-line-height),
    var(--gsc-product-old-price-font-weight)
  );
}

.product-price__old-price--highlight-sale {
  text-decoration-color: var(--gsc-product-highlight-price-color);
}
