.float-element {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  display: block;

  transition: opacity 0.2s ease-in-out;
}

.float-element--fixed {
  position: fixed;
}

.float-element[aria-hidden='true'] {
  display: none;
}

.float-element[aria-hidden='true'],
.float-element[aria-hidden='true'] * {
  pointer-events: none !important;
}
