.rating {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 0.6rem;
  row-gap: 0.6rem;
  color: var(--gsc-text-color-70);
}

.rating--with-inline-star {
  display: inline-flex;
}

.rating__count {
  @include font(
    var(--gsc-fz-12),
    var(--gsc-lh-5),
    var(--gsc-font-weight-normal)
  );
}

.rating__stars {
  display: flex;
  align-items: center;
  column-gap: 0.2rem;
}

.rating__stars--inline {
  display: inline-flex;
}

.rating__star {
  @include box(1.8rem, 1.8rem);
}

.rating--with-inline-star .rating__star {
  display: inline-block;
  margin-bottom: -0.3rem;
}

.rating__star path {
  fill: var(--gsc-rating-stars-color, #f49a13);
}

.rating__star--transparent path {
  fill: var(--gsc-rating-stars-color, #f49a13);
  opacity: 0.35;
}
