.search-field {
  position: relative;
  display: block;
  width: 100%;
  padding-right: 4rem;
  padding-bottom: 1.2rem;
  border-bottom: 1px solid var(--gsc-text-color-100);
}

.search-field__input {
  @include box(100%, 2.4rem);
  padding: 0;
  color: var(--gsc-text-color-100);
  background: transparent;
  border: none;
  outline: none;

  @include font(var(--gsc-fz-11), var(--gsc-lh-4), var(--gsc-font-weight-bold));
}

.search-field__input::placeholder {
  color: var(--gsc-text-color-35);
  font-weight: var(--gsc-font-weight-bold);
}

.search-field__controls {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  column-gap: 1.2rem;
}

.search-field__controls-item-icon {
  @include box(2.4rem, 2.4rem);
}

.search-field__controls-item-icon--reset {
  opacity: 0.35;
}

@media (--md-screen) {
  .search-field__controls-item-icon--reset:hover {
    opacity: 1;
  }
}
