.featured-article {
  display: flex;
  flex-flow: column-reverse;
  color: var(--gsc-text-color-100);
  border-radius: var(--gsc-base-radii, 0);
}

.featured-articles--color-background {
  background: var(--gsc-background-color-100);
}

.featured-articles__carousel-viewport {
  z-index: 1;
  border-radius: var(--gsc-base-radii, 0);
}

.featured-articles__carousel-viewport::before {
  display: none;
  content: '{ "draggable": false }';
}

.featured-article__details-wrap {
  position: relative;
  display: flex;
  flex-grow: 1;
  align-items: flex-start;
  padding: 3.2rem 3.2rem 5.2rem;
}

.featured-article__order-number {
  position: absolute;
  top: 0;
  left: 0;
}

.featured-article__details {
  position: relative;
  display: flex;
  align-items: center;
}

.featured-article__order-number + .featured-article__content {
  padding: 6.4rem 0 0;
}

.featured-article__content > * + * {
  margin: 1.6rem 0 0;
}

.featured-articles__btns-wrap {
  position: absolute;
  top: 29.3rem;
  right: 2rem;
}

.featured-articles__bg {
  position: absolute;
  inset: 0;
  background-image: linear-gradient(
    to right,
    var(--gsc-foreground-color-100),
    var(--gsc-foreground-color-100)
  );
  background-repeat: no-repeat;
  border-radius: var(--gsc-base-radii);
}

.featured-article__meta {
  @include font(
    var(--gsc-fz-14),
    var(--gsc-lh-6),
    var(--gsc-font-weight-normal)
  );
}

.featured-article__text {
  @include lineClamp(4);
  @include font(
    calc(var(--gsc-body-font-scale) * var(--gsc-fz-12)),
    var(--gsc-lh-5),
    var(--gsc-font-weight-normal)
  );
}

.featured-article__meta-item + .featured-article__meta-item::before {
  display: inline-block;
  margin: 0 0.5rem 0 0.4rem;
  font-size: 1.6rem;
  content: '\2022';
}

.featured-articles__btns {
  position: relative;
  z-index: 2;
  display: flex;
  column-gap: 0.7rem;
}

.featured-articles__carousel-btn {
  @include box(4rem, 4rem);
}

.featured-articles__carousel-btn-chevron {
  @include box(1.71rem, 1.71rem);
}

.featured-article__link {
  overflow: hidden;
  height: 31.3rem;
}

.round-style .featured-article__link {
  border-radius: var(--gsc-base-radii) var(--gsc-base-radii) 0rem 0rem;
}

.featured-article__image {
  @include box(100%, 100%);
  object-fit: cover;
}

@media (--md-screen) {
  .featured-article__meta {
    @include font(var(--gsc-fz-12), var(--gsc-lh-5));
  }

  .featured-article {
    flex-flow: row;
    height: calc(100% - 12rem);
  }

  .featured-article__text {
    @include font(
      calc(var(--gsc-body-font-scale) * var(--gsc-fz-11)),
      var(--gsc-lh-4),
      var(--gsc-font-weight-normal)
    );
  }

  .featured-article__image {
    height: 100%;
  }

  .featured-article__link {
    height: calc(100% + 12rem);
  }

  .round-style .featured-article__link {
    border-radius: var(--gsc-base-radii);
  }

  .featured-article__image--placeholder,
  .featured-article__link {
    flex: 1;
    height: 100%;
  }

  .featured-articles__carousel-container {
    height: 80rem;
  }

  .featured-article__details {
    width: 100%;
    max-width: 59.2rem;
    margin: 0 auto;
  }

  .featured-article__details-wrap {
    flex-grow: 0;
    flex-shrink: 0;
    align-items: center;
    @include box(50%, 100%);
    padding: 6.4rem 4.8rem 18.4rem;
  }

  .featured-article__content {
    flex: 1;
  }

  .featured-article__content > * + * {
    margin: 2.4rem 0 0;
  }

  .featured-article__order-number + .featured-article__content {
    padding: 6.8rem 0 1.6rem;
  }

  .featured-articles__btns-wrap {
    top: 0;
    right: unset;
    left: 0;
    display: flex;
    align-items: flex-end;
    @include box(50%, 100%);
    padding: 6.4rem 4.8rem 9.2rem;
  }

  .featured-articles__carousel-btn {
    @include box(5.6rem, 5.6rem);
  }

  .featured-articles__carousel-btn-chevron {
    @include box(2.4rem, 2.4rem);
  }

  .featured-articles__btns {
    width: 100%;
    max-width: 59.2rem;
    margin: 0 auto;
    column-gap: 1.2rem;
  }

  .featured-articles__bg {
    height: calc(100% - 12rem);
  }
}
