.cart-count:empty {
  display: none;
}

.cart-count {
  position: absolute;
  top: -5px;
  left: calc(100% - 11px);
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 1.6rem;
  height: 1.6rem;
  padding: 0 0.4rem;
  color: var(--gsc-header-cart-icon-text-color);
  background-color: var(--gsc-header-cart-icon-background-color);
  border-radius: 50%;

  @include font(1.2rem, 1.6rem, var(--gsc-font-weight-bold));
}

.cart-count--ellipse {
  border-radius: 8px;
}
