.cart-subtotal {
  width: 100%;
}

.cart-subtotal__totals {
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 1.6rem;
}

.cart-subtotal__items {
  margin-bottom: 1.2rem;
}

.cart-subtotal__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 0.8rem;
}

.cart-subtotal__item-name {
  @include font(
    var(--gsc-fz-13),
    var(--gsc-lh-16),
    var(--gsc-font-weight-medium)
  );
}

.cart-subtotal__sale {
  display: block;
  overflow: hidden;
}

.cart-subtotal__sale-content-wrap {
  display: block;
  overflow: hidden;
  opacity: 0;
  transition-timing-function: ease;
  transition-duration: 0.25s;
  transition-property: max-height, opacity;
  pointer-events: none;
}

.cart-subtotal__sale[data-aria-expanded='true']
  .cart-subtotal__sale-content-wrap {
  opacity: 1;
  pointer-events: all;
}

.cart-subtotal__sale-content {
  display: flex;
  flex-flow: column;
  row-gap: 0.4rem;
  padding-top: 0.8rem;
}

.cart-subtotal__sale-label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 0.8rem;
  @include font(
    var(--gsc-fz-13),
    var(--gsc-lh-16),
    var(--gsc-font-weight-medium)
  );
  cursor: pointer;
}

.cart-subtotal__sale-label-name {
  display: flex;
  align-items: center;
  column-gap: 0.4rem;
}

.cart-subtotal__sale-label-amount {
  @include font(
    var(--gsc-fz-13),
    var(--gsc-lh-16),
    var(--gsc-font-weight-bold)
  );
}

.cart-subtotal__sale-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--gsc-text-color-70);

  @include font(
    var(--gsc-fz-14),
    var(--gsc-lh-15),
    var(--gsc-font-weight-normal)
  );
}

.cart-subtotal__sale-item-name {
  padding-left: 0.8rem;
}

.cart-subtotal__sale-label-icon {
  transform: rotate(90deg);
  transition: transform 250ms ease;
  width: 2rem;
  height: 2rem;
}

.cart-subtotal__sale[data-aria-expanded='true']
  .cart-subtotal__sale-label-icon {
  transform: rotate(-90deg);
}

.cart-subtotal__item-amount {
  @include font(
    var(--gsc-fz-13),
    var(--gsc-lh-16),
    var(--gsc-font-weight-bold)
  );
}

.cart-subtotal__taxes {
  margin-top: 1.2rem;
  color: var(--gsc-text-color-70);

  @include font(
    var(--gsc-fz-14),
    var(--gsc-lh-15),
    var(--gsc-font-weight-normal)
  );
}

.cart-subtotal__subtotals-cart-item-count {
  color: var(--gsc-text-color-35);
}

.cart-subtotal__accordeon-content {
  padding: 0 2.4rem 2.4rem;

  @include font(var(--gsc-fz-12), var(--gsc-lh-5));
}

.cart-subtotal__discounts {
  display: flex;
  flex-flow: column;
  row-gap: 0.8rem;
}

@media (--md-screen) {
  .cart-subtotal__accordeon-plus[data-aria-expanded='true'] {
    border-bottom-color: transparent;
  }

  .cart-subtotal__accordeon-plus-btn {
    padding-right: 0;
    padding-left: 0;

    @include font(var(--gsc-fz-11), var(--gsc-lh-4));
  }

  .cart-subtotal {
    max-width: 42.4rem;
    margin-left: auto;
  }

  .cart-subtotal__accordeon-plus-btn {
    padding: 2rem 0;
  }
}
