.carousel-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  @include box(5.6rem, 5.6rem);
  color: var(--gsc-black-color-100);
  background-color: var(--gsc-white-color-50);
  border: none;
  border-radius: 50%;
  box-shadow: 0 0.4rem 1.6rem rgb(0, 0, 0, 0.08);
  cursor: pointer;
  backdrop-filter: blur(4px);
  transition: background-color 0.25s ease-in-out;
}

.carousel-btn__icon {
  @include box(2.4rem, 2.4rem);
}

.carousel-btn[disabled] {
  background-color: var(--gsc-white-color-30);
  color: var(--gsc-black-color-30);
  cursor: not-allowed;
}

.carousel-btn--large {
  @include box(8rem, 8rem);
}

.carousel-btn__icon--large {
  @include box(3rem, 3rem);
}

@media (--md-screen) {
  .carousel-btn:not([disabled]):hover {
    background-color: var(--gsc-white-color-80);
  }
}
