.order__header {
  padding: 0 0 4rem;
  text-align: center;
  border-bottom: 1px solid var(--gsc-border-color-100);
}

.order-history__header {
  margin: 0 0 4rem;
  text-align: center;
}

.order__desktop-table {
  display: none;
}

.order__items {
  border-top: 1px solid var(--gsc-border-color-100);
}

.order__item {
  display: flex;
  flex-flow: column;
  row-gap: 2.4rem;
  border-bottom: 1px solid var(--gsc-border-color-100);
}

.order__item,
.order__desktop-table {
  padding: 3.2rem 0;
}

.order__item-field {
  display: flex;
  justify-content: space-between;
  gap: 3rem;

  @include font(var(--gsc-fz-12), var(--gsc-lh-5));
}

.order__item-field-label {
  font-weight: var(--gsc-font-weight-bold);
}

.order__item-field-value {
  font-weight: var(--gsc-font-weight-medium);
  text-align: right;
}

.order__table-row {
  border-color: transparent;
}

.order__info {
  display: flex;
  flex-flow: column;
  padding: 3.2rem 0;
}

.order__info-order-date {
  margin: 0 0 2.4rem;
  color: var(--gsc-text-color-70);

  @include font(
    var(--gsc-fz-12),
    var(--gsc-lh-5),
    var(--gsc-font-weight-medium)
  );
}

.order__info-order-icon {
  @include box(2.4rem, 2.4rem);
  margin-top: 0.8rem;
}

.order__info-title {
  margin: 0 0 1.6rem;
}

.order__info-order-content {
  color: var(--gsc-red-color-100);

  @include font(
    var(--gsc-fz-12),
    var(--gsc-lh-14),
    var(--gsc-font-weight-medium)
  );
}

.order__info-order-alert {
  display: flex;
  padding: 2.4rem;
  column-gap: 1.6rem;
  background-color: var(--gsc-foreground-color-100);
  border-radius: var(--gsc-base-radii, 0);
}

.order__footer {
  margin: 3.2rem 0;
}

.order__footer-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 2.4rem;
}

.order__footer-item--small + .order__footer-item--small {
  margin: 0.8rem 0 0;
}

.order__footer-item--small {
  color: var(--gsc-text-color-70);

  @include font(
    var(--gsc-fz-12),
    var(--gsc-lh-5),
    var(--gsc-font-weight-medium)
  );
}

.order__footer-item--small + .order__footer-item--small {
  margin: 0.8rem 0 0;
}

.order__footer-item--large {
  margin: 3.2rem 0 0;

  @include font(var(--gsc-fz-10), var(--gsc-lh-2), var(--gsc-font-weight-bold));
}

.order__status {
  display: flex;
  flex-flow: column;
  row-gap: 2.4rem;
}

.order__status-item-title {
  margin: 0 0 2.4rem;

  @include font(var(--gsc-fz-11), var(--gsc-lh-4), var(--gsc-font-weight-bold));
}

.order__status-item {
  flex: 1;
  padding: 4rem;
  background-color: var(--gsc-foreground-color-100);
  border-radius: var(--gsc-base-radii);
}

.order__status-item-info {
  color: var(--gsc-text-color-70);

  @include font(
    var(--gsc-fz-12),
    var(--gsc-lh-5),
    var(--gsc-font-weight-medium)
  );
}

.order__status-item-info + .order__status-item-info {
  margin: 1.6rem 0 0;
}

@media (--md-screen) {
  .order__header {
    padding: 0 0 2.4rem;
    text-align: left;
    border: none;
  }

  .order-history__header {
    margin: 0 0 4rem;
    text-align: left;
  }

  .order__info {
    padding: 4.8rem 0 4rem;
  }

  .order__info-title {
    margin: 0 0 1.2rem;
  }

  .order__status {
    flex-flow: row;
    column-gap: 2.4rem;
  }

  .order__desktop-table {
    display: block;
  }

  .order__items {
    display: none;
  }

  .order__table-row {
    border-color: var(--gsc-border-color-100);
  }

  .order__desktop-table {
    padding: 0;
  }
}
