.article-card {
  display: block;
  position: relative;
  overflow: hidden;
  color: var(--gsc-text-color-100);
}

.article-card--with-background {
  background: var(--gsc-foreground-color-100);
  border-radius: var(--gsc-base-radii);
  min-height: 100%;
}

.article-card__title {
  max-width: 100%;
  overflow: hidden;

  -webkit-line-clamp: 3;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  display: -webkit-box;
}

.article-card__link {
  position: absolute;
  inset: 0;
  z-index: 1;
  text-decoration: none;
}

.article-card__image-wrapper {
  /* z-index: 1; for work overflow:hidden + border-radius for child on iphone https://bugs.webkit.org/show_bug.cgi?id=98538 */
  overflow: hidden;
  border-radius: var(--gsc-base-radii);
}

.article-card__meta {
  @include font(
    var(--gsc-fz-12),
    var(--gsc-lh-5),
    var(--gsc-font-weight-normal)
  );
}

.article-card__meta-item + .article-card__meta-item::before {
  margin: 0 0.5rem 0 0.4rem;
  font-size: 1.6rem;
  content: '\2022';
}

.article-card__description {
  @include lineClamp(3);
}

.article-card__tags-container {
  display: flex;
  align-items: flex-start;
  width: 100%;
  height: 100%;
  padding: 1.2rem 0.8rem 1.2rem 1.2rem;
  transition: opacity 0.2s ease-in-out;
}

.article-card__tags-component {
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}

.article-card__tags-component[visible] {
  opacity: 1;
}

.article-card__tags-container--under-image {
  padding: 0rem 0.8rem 0.8rem 0;
}

.article-card__tags-container--on-image {
  position: absolute;
  top: 0;
  left: 0;
}

.article-card__tags {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  max-height: 100%;
  column-gap: 0.8rem;
  row-gap: 0.8rem;
}

.article-card__tags--with-collapse {
  flex-wrap: nowrap;
}

.article-card__tag {
  border-radius: var(--gsc-badge-radii, 0);
  color: var(--gsc-text-color-100);
  background: var(--gsc-foreground-color-100);

  @include font(
    var(--gsc-article-card-badge-font-size),
    var(--gsc-article-card-badge-line-height),
    var(--gsc-article-card-badge-font-weight)
  );

  z-index: 3;
  min-width: fit-content;

  transition: background-color 0.2s ease-in-out;
}

.article-card__extend-button {
  position: absolute;
  z-index: 3;
}

.article-card__tag--with-heght-collapse[tag-hidden] {
  position: absolute;
  opacity: 0;
  pointer-events: none;
  width: max-content;
}

.article-card__extend-button--with-heght-collapse {
  position: relative;
  z-index: 3;
}

.article-card__tag:hover {
  background: var(--gsc-foreground-color-100);
}

.article-card__tag[tag-hidden] {
  opacity: 0;
  pointer-events: none;
  width: max-content;
}

.article-card__tag > a {
  display: block;
  text-decoration: none;
  padding: 0.6rem 1rem;
}

.article-card__tags-container--under-image > .show-more-container {
  bottom: 0;
}

.show-more {
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 0.2rem;
  cursor: pointer;
  height: 32px;
  width: 32px;
}

.show-more__dot {
  background: var(--gsc-text-color-100);
  width: 0.4rem;
  height: 0.4rem;
  border-radius: 50%;
}

.article-card__dropdown {
  position: fixed;
  width: 22rem;
  background: var(--gsc-background-color-100);
}

.article-card__dropdown-body {
  display: flex;
  flex-wrap: wrap;
  padding: 2rem;
  column-gap: 0.8rem;
  row-gap: 0.8rem;
  background: var(--gsc-background-color-100);
}

.article-card__dropdown-body .article-card__tag {
  background: transparent;
}

.article-card__dropdown-body .article-card__tag:hover {
  background: var(--gsc-foreground-color-100);
}

.article-card__details {
  position: relative;
  padding: 1.6rem 1.2rem 0 0;
}

.article-card__details--left-spacing {
  padding-left: 1.2rem;
}

.article-card__details--with-background {
  padding: 2.4rem 2.4rem 3.2rem 2.4rem;
}

.article-card__details--with-background .article-card__tag {
  background: var(--gsc-background-color-100);
}

.article-card__details--bottom-spacing {
  padding-bottom: 4rem;
}

.article-card__details > * + * {
  margin: 0.8rem 0 0;
}

.article-card__details > * + .article-card__read-more-link {
  margin: 2rem 0 0;
}

.article-card__read-more-link {
  gap: 8px;
}

@media (--md-screen) {
  .article-card__details {
    padding: 1.6rem 1.6rem 0 0;
  }

  .article-card__details--left-spacing {
    padding-left: 1.6rem;
  }

  .article-card__details--bottom-spacing {
    padding-bottom: 4.8rem;
  }

  .article-card__details > * + * {
    margin: 1.2rem 0 0;
  }

  .article-card__details--with-background {
    padding: 2.4rem 2.4rem 3.2rem 2.4rem;
  }

  .article-card__tags-container--under-image {
    padding: 0rem 0.8rem 0 0;
  }
}
