.password-footer {
  padding: 2rem 0;
}

.password-footer__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  column-gap: 1rem;
  row-gap: 1rem;
}

.password-footer__shopify-logo {
  width: 7.8rem;
  margin: 0 0 0 0.9rem;
  color: var(--gsc-text-color-70);
}

.password-footer__item {
  display: flex;
  align-items: center;
  color: var(--gsc-text-color-70);

  @include font(var(--gsc-fz-14), var(--gsc-lh-6));
}

.password-footer__copyright.footer__copyright,
.password-footer__policies.footer__policies {
  min-width: 100%;
  padding: 2rem 0 0 0;
  justify-content: center;
}

.password-footer__login-link {
  color: var(--gsc-text-color-100);
}

@media (--md-screen) {
  .password-footer {
    padding: 2.4rem 0;
  }

  .password-footer__container {
    flex-flow: row;
    flex-wrap: wrap;
  }
}
