.classic-pagination {
  display: flex;
  justify-content: center;
}

.classic-pagination__list {
  display: flex;
  margin: 0;
  padding: 0;
  column-gap: 0.2rem;
  list-style: none;
}

.classic-pagination__item {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.9rem 1.2rem;
  cursor: pointer;

  @include font(var(--gsc-fz-12), var(--gsc-lh-5), var(--gsc-font-weight-medium));
  
  border-radius: var(--gsc-classic-pagination-item-radii);
  overflow: hidden;
}

.classic-pagination__link.active {
  color: var(--gsc-background-color-100);
  background-color: var(--gsc-text-color-100);
}

@media (--md-screen) {
  .classic-pagination__link:hover {
    color: var(--gsc-background-color-100);
    background-color: var(--gsc-text-color-100);
  }
}
