.gift-page {
  display: flex;
  flex-flow: column;
  min-height: 100vh;
}

.gift-page__header {
  display: flex;
  justify-content: center;
  padding: 3rem 0;
}

.gift-page__container {
  display: flex;
  flex: 1;
  flex-flow: column;
  align-items: center;
  justify-content: center;
}

.gift-page__content {
  margin: 0 0 3.2rem;
  text-align: center;
}

.gift-page__title {
  margin: 0 0 1.6rem;
}

.gift-page__text {
  color: var(--gsc-text-color-70);
  @include font(
    calc(var(--gsc-body-font-scale) * var(--gsc-fz-11)),
    var(--gsc-lh-4)
  );
}

.gift-page__image-wrap {
  position: relative;
  max-width: 100%;
  @include box(40rem, 26.6rem);
}

.gift-page__image {
  @include box(100%, 100%);
  object-fit: cover;
  border-radius: 20px;
  box-shadow: 0 4rem 2.8rem -2.4rem var(--gsc-black-color-5),
    0 3.6rem 6rem -1.6rem var(--gsc-black-color-35);
}

.gift-page__image--expired {
  background-color: var(--gsc-white-color-100);
  mix-blend-mode: luminosity;
}

.gift-page__price {
  position: absolute;
  top: 16px;
  left: 16px;
  z-index: 1;
  padding: 0.4rem 1.6rem;
  color: var(--gsc-white-color-100);
  text-shadow: -1px -1px 3px var(--gsc-white-color-25),
    1px 1px 3px var(--gsc-black-color-25);
  background: linear-gradient(
    111.49deg,
    var(--gsc-white-color-40) -8.95%,
    rgb(var(--gsc-white-color), 0.01) 114%
  );
  border: 1px solid var(--gsc-white-color-35);
  border-radius: 10px;
  backdrop-filter: blur(7.5px);

  @include font(var(--gsc-fz-9), var(--gsc-lh-13), var(--gsc-font-weight-bold));
}

.gift-page__number {
  display: none;
}

.gift-page__copy-code {
  position: absolute;
  right: 16px;
  bottom: 16px;
  left: 16px;
  z-index: 1;
  display: block;
  padding: 1rem 1.6rem;
  color: var(--gsc-white-color-100);
  text-align: center;
  text-shadow: -1px -1px 3px rgb(218, 35, 81, 0.1),
    1px 1px 3px var(--gsc-black-color-25);
  background: linear-gradient(
    111.49deg,
    var(--gsc-white-color-40) -8.95%,
    rgb(255, 255, 255, 0.01) 114%
  );
  border: 1px solid var(--gsc-white-color-35);
  border-radius: 10px;
  cursor: pointer;
  backdrop-filter: blur(7.5px);

  @include font(var(--gsc-fz-10), var(--gsc-lh-2), var(--gsc-font-weight-bold));
}

.gift-page__qr-code {
  display: flex;
  justify-content: center;
  margin: 4rem 0 0;
  padding: 0.5rem;
  background-color: var(--gsc-white-color-100);
}

.gift-page__btns {
  display: flex;
  flex-flow: column;
  row-gap: 1.6rem;
  margin: 3.2rem 0 0;
}

.gift-page__print-btn {
  color: var(--gsc-text-color-35);
  column-gap: 1rem;
  border: none;
}

.gift-page__btn {
  width: 100%;
}

@media (--md-screen) {
  .gift-page__btn {
    width: auto;
  }

  .gift-page__print-btn:not([disabled]):hover {
    box-shadow: none;
  }
}
