.product-details {
  display: block;
}

.product-details__container {
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.product-details__container--with-aside-spacing {
  padding: 2rem;
}

.round-style .product-details__container--with-aside-spacing {
  overflow: hidden;
  border-radius: var(--gsc-base-radii, 0);
}

.product-details__accordeon-btn {
  padding-left: 0;
}

.product-details__accordeon-content {
  padding-top: 1.2rem;
}

.product-details__image {
  width: 100%;
  max-width: 100%;
}

.product-details__rte ul,
.product-details__rte ol {
  padding: 0 0 0 2.8rem;
}

.product-details__tab-names {
  align-items: center;
  column-gap: 3.2rem;

  display: flex;
  padding: 0 0 4rem;
  overflow: auto;
  min-width: 100vw;
  margin-left: calc(-1 * var(--gsc-container-padding));
  padding-inline: var(--gsc-container-padding);
  scrollbar-width: none;
}

.product-details__tab-name {
  display: flex;
  align-items: center;
  padding-bottom: 1.6rem;
  column-gap: 0.8rem;
  border-bottom: 1px solid transparent;
  text-decoration: none;
  color: var(--gsc-text-color-35);
  cursor: pointer;
  text-wrap: nowrap;
}

.product-details__tab-name.selected {
  color: var(--gsc-text-color-100);
}

.product-details__tab-name-icon {
  @include box(var(--gsc-icon-size), var(--gsc-icon-size));
  object-fit: contain;
}

.product-details__tab-name-carousel-slide {
  min-width: fit-content;
}

.product-details__tab-name.selected {
  color: var(--gsc-text-color-100);
  border-color: var(--gsc-text-color-100);
}

.product-details__content-tab-content {
  display: flex;
  flex-flow: column;
  row-gap: 2.4rem;
}

@media (--md-screen) {
  .product-details__tab-name:hover {
    color: var(--gsc-text-color-100);
  }

  .product-details__container {
    padding-top: 2.8rem;
    padding-bottom: 2.8rem;
  }

  .product-details__container--with-aside-spacing {
    padding: 2.8rem;
  }

  .product-details__tab-names {
    display: flex;
    padding: 0 0 4.8rem;
    column-gap: 5.6rem;

    overflow: hidden;
    min-width: 0;
    margin-left: 0;
    padding-inline: 0;
  }

  .product-details__tab-name-carousel {
    display: none;
  }

  .product-details__tab-name:hover {
    color: var(--gsc-text-color-100);
  }

  .product-details__image {
    width: var(--gsc-image-width);
  }
}

@media (--lg-screen) {
  .product-details__container {
    padding-top: 3.2rem;
    padding-bottom: 3.2rem;
  }

  .product-details__container--with-aside-spacing {
    padding: 3.2rem;
  }
}

@media (--xl-screen) {
  .product-details__container {
    padding-top: 4rem;
    padding-bottom: 4rem;
  }

  .product-details__container--with-aside-spacing {
    padding: 4rem;
  }
}

@media (--xxl-screen) {
  .product-details__container {
    padding-top: 4.4rem;
    padding-bottom: 4.4rem;
  }

  .product-details__container--with-aside-spacing {
    padding: 4.4rem;
  }
}
