.product-breadcrumbs {
  background: var(--gsc-background-color-100);
}

.product-breadcrumbs-block {
  display: block;

  @include font(var(--gsc-fz-12), var(--gsc-lh-5));
}

@media (--md-screen) {
  .product-breadcrumbs-block__link:hover {
    color: var(--gsc-text-color-100);
    text-decoration: underline;
  }
}
