.table {
  width: 100%;
  text-align: left;
}

.table__row {
  display: flex;
  align-items: center;
  padding: 1.2rem;
  column-gap: 1.6rem;
  border-bottom: 1px solid var(--gsc-border-color-100);
}

.table__row--header {
  padding-top: 0;
}

.table__column {
  @include font(var(--gsc-fz-12), var(--gsc-lh-5), var(--gsc-font-weight-medium));
}

.table__heading--right-align,
.table__column--right-align {
  text-align: right;
}

.table__heading {
  font-weight: var(--gsc-font-weight-bold);
}

.table__heading,
.table__column {
  flex: 1 1 15.6rem;
}

.table__heading--large,
.table__column--large {
  flex: 1 0 26rem;
}

@media (--md-screen) {
  .table__row {
    padding: 1.6rem 0;
  }

  .table__row--header {
    padding-top: 0;
  }
}
