.cart-item__option:empty {
  display: none;
}

.cart-item {
  display: block;
  align-items: center;
  padding: 0;
  overflow: hidden;
  transition: max-height 0.25s linear;
}

.cart-item.is-new {
  max-height: 0;
}

.cart-item__checkmark-with-chevron {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.cart-item__body {
  border-top: 1px solid transparent;
}

.cart-item__name {
  position: relative;
  color: var(--gsc-text-color-100);
  font-weight: var(--gsc-product-card-title-font-weight);
  font-size: var(--gsc-fz-12);
  font-family: var(--gsc-product-card-title-font-family);
  font-style: var(--gsc-product-card-title-font-style);
  text-transform: var(--gsc-product-card-title-transform);
  line-height: var(--gsc-lh-5);
  text-decoration: none;
}

.cart-item__name-label {
  border-bottom: 1px solid var(--gsc-text-color-0);
  transition: border-color 0.2s ease-in-out;
}

.cart-item__grid {
  position: relative;
  display: flex;
  align-items: flex-start;
  column-gap: 1.6rem;
}

.cart-item__product {
  display: flex;
  align-items: center;
  width: 50%;
}

.cart-item__price {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  column-gap: 0.8rem;

  @include font(var(--gsc-fz-12), var(--gsc-lh-5), var(--gsc-font-weight-bold));
}

.cart-item__price-item--small {
  opacity: 0.35;
  font-weight: var(--gsc-font-weight-medium);
}

.cart-item__price-item--old {
  text-decoration: line-through;
}

.cart-item__totals {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.cart-item__image-wrapper {
  border-radius: var(--gsc-base-radii);
}

.cart-item__option {
  display: flex;
  flex-wrap: wrap;
  color: var(--gsc-text-color-80);

  @include font(
    var(--gsc-fz-12),
    var(--gsc-lh-5),
    var(--gsc-font-weight-normal)
  );
}

.cart-item__info {
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  margin: 0 0 1.2rem;
  padding-right: 2.4rem;
  row-gap: 0.4rem;
}

.cart-item__cost-wrap {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  column-gap: 1.6rem;
  row-gap: 1.6rem;
}

.cart-item__cost-wrap .cart-item__price-wrap {
  display: none;
}

.cart-item__price-wrap {
  position: relative;
}

.cart-item__info--hide-mobile {
  display: none;
}

.cart-item__remove-button {
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
}

.is-cart-loading .cart-item__remove-button {
  pointer-events: none;
  cursor: not-allowed;
}

.cart-item__remove-button-icon {
  @include box(2.4rem, 2.4rem);

  opacity: 0.35;
}

.cart-item__link {
  position: relative;
  flex-shrink: 0;
  width: 10.8rem;
}

.cart-item__pay-info {
  display: flex;
  flex: 1;
  flex-flow: column;
  margin: 0 0 0.8rem;
  overflow: hidden;
}

.cart-item__discounts {
  margin: 0.2rem 0 0;
}

.loading-spinner.cart-item__price-loading-spinner {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;

  @include box(2.4rem, 2.4rem);
}

.cart-item[is-loading] .cart-item__price-loading-spinner + .cart-item__price {
  opacity: 0;
}

.cart-item[is-loading] .loading-spinner.cart-item__price-loading-spinner {
  display: block;
}

.cart-item__totals .loading-spinner.cart-item__price-loading-spinner {
  left: unset;
  right: 0;
}

.cart-item-quantity {
  display: flex;
  column-gap: 0.4rem;
  height: 3.2rem;
}

.cart-item-quantity--with-rounded-buttons {
  column-gap: 0.4rem;
}

.cart-item-quantity__button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--gsc-foreground-color-100);
  border-radius: 50%;
  border: none;
  cursor: pointer;
  width: 3.2rem;
  height: 3.2rem;
  padding: 0.4rem;
  appearance: none;
  color: var(--gsc-text-color-100);
}

.cart-item-quantity__button-icon {
  @include box(2.4rem, 2.4rem);
}

.cart-item-quantity__input {
  width: 100%;
  padding: 0.6rem 0.2rem;
  color: var(--gsc-text-color-100);
  background-color: transparent;
  text-align: center;
  border: none;
  border-radius: 0;
  width: 3.2rem;
  height: 3.2rem;

  @include font(
    var(--gsc-fz-13),
    var(--gsc-lh-16),
    var(--gsc-font-weight-medium)
  );
}

.cart-item-quantity__input:focus-visible {
  outline: none;
}

.cart-item-quantity__input::-webkit-outer-spin-button,
.cart-item-quantity__input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

.cart-item-quantity__input[type='number'] {
  appearance: textfield;
}

.cart-item-quantity__input,
.cart-item-quantity__button {
  height: 100%;
}

.cart-item-quantity__button[disabled] {
  cursor: not-allowed;
}

.cart-item-quantity__button[disabled]:active {
  pointer-events: none;
}

.cart-item-quantity__button[disabled] .cart-item-quantity__button-icon {
  opacity: 0.35;
}

@media (--md-screen) {
  .cart .cart-item__cost-wrap .cart-item__price-wrap {
    display: block;
  }

  .cart .cart-item__totals .cart-item__price {
    flex-flow: column;
    align-items: flex-end;
    @include font(
      var(--gsc-fz-11),
      var(--gsc-lh-4),
      var(--gsc-font-weight-bold)
    );
  }

  .cart-item-quantity__button:hover {
    background: linear-gradient(
        0deg,
        rgb(var(--gsc-text-color), 0.08),
        rgb(var(--gsc-text-color), 0.08)
      ),
      var(--gsc-foreground-color-100);
  }

  .cart-item__name:hover .cart-item__name-label {
    border-color: var(--gsc-text-color-100);
  }

  .cart-item__remove-button-icon:hover {
    opacity: 1;
  }

  .cart-item__grid {
    column-gap: 2.4rem;
  }

  .cart .cart-item-quantity__input {
    width: 4.4rem;
  }
}
