.comment-form__title {
  @include font(var(--gsc-fz-10), var(--gsc-lh-2), var(--gsc-font-weight-bold));
}

.comment-form__note {
  margin: 3.2rem 0 0;
  color: var(--gsc-text-color-70);

  @include font(var(--gsc-fz-14), var(--gsc-lh-7), var(--gsc-font-weight-medium));
}

.comment-form__btn {
  width: 100%;
  margin: 3.2rem 0 0;
}

.comment-form__body {
  display: flex;
  flex-flow: column;
  row-gap: 2rem;
  margin: 3.2rem 0 0;
}

.comment-form__field-group {
  row-gap: 2rem;
  column-gap: 2rem;
}

@media (--md-screen) {
  .comment-form__body {
    row-gap: 2.4rem;
  }

  .comment-form__field-group {
    row-gap: 2.4rem;
    column-gap: 2.4rem;
  }

  .comment-form__btn {
    width: auto;
  }
}
