.quantity {
  display: flex;
  align-items: center;
}

.quantity__btn {
  flex-shrink: 0;
  padding: 0;
  color: var(--gsc-text-color-100);
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.quantity__btn-wrapper {
  width: 2.4rem;
  height: 2.4rem;
}

.quantity__btn-icon {
  width: 2.4rem;
  height: 2.4rem;
}

.quantity__input[disabled],
.quantity__btn[disabled] {
  cursor: not-allowed;
  opacity: 0.35;
}

.quantity__btn[disabled]:active {
  pointer-events: none;
}

.quantity__input {
  flex-grow: 1;
  width: 100%;
  padding: 0 0.5rem;
  color: var(--gsc-text-color-100);
  text-align: center;
  background-color: transparent;
  border: 0;
  appearance: none;

  @include font(var(--gsc-fz-12), var(--gsc-lh-5), var(--gsc-font-weight-bold));
}

.quantity__input {
  -moz-appearance: textfield; /* Firefox */
  appearance: textfield;
}

.quantity__input::-webkit-outer-spin-button,
.quantity__input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}

.quantity__input:focus-visible,
.quantity__input:focus {
  outline: none;
  box-shadow: none;
}
