.collection__header {
  display: flex;
  flex-flow: column;
  margin: 0 0 3.2rem;
  column-gap: 2.8rem;
  row-gap: 2rem;
}

.collection__header--center {
  text-align: center;
}

.collection__header--right {
  text-align: right;
}

.collection__header--with-tabs {
  margin: 0 0 2.4rem;
}

.collection__tab-carousel {
  display: flex;
  align-items: center;

  --gsc-space-between-cards: 3.2rem;
}

.collection__tab-carousel--center .carousel__viewport {
  width: fit-content;
  margin-inline: auto;
  max-width: 100%;
}

.collection__tab-carousel--right .carousel__viewport {
  width: fit-content;
  margin-left: auto;
  max-width: 100%;
}

.collection__tab-list {
  display: none;
}

.collection__tab-name {
  flex: 0 0 fit-content;
  min-width: 0;
  font-weight: var(--gsc-font-weight-medium);
}

@media (--md-screen) {
  .collection__header {
    flex-flow: row wrap;
    align-items: baseline;
    justify-content: space-between;
    margin: 0 0 4rem;
    column-gap: 4.8rem;
  }

  .collection__header--center {
    flex-flow: column;
    align-items: center;
    justify-content: center;
  }

  .collection__header--right {
    flex-flow: column;
    align-items: flex-end;
    justify-content: center;
  }

  .collection__tab-carousel {
    display: none;
  }

  .collection__tab-list {
    display: flex;
    flex-wrap: wrap;
    column-gap: 4rem;
    row-gap: 1.6rem;
  }
}
