.social-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}

.social-list__item-link {
  display: flex;
  align-items: center;
  justify-content: center;
  @include box(4.4rem, 4.4rem);
  color: var(--gsc-text-color-100);
}

.social-list__item-link-icon {
  @include box(1.8rem, 1.8rem);
}

@media (--md-screen) {
  .social-list__item:hover {
    opacity: 0.5;
  }
}
