.shop-mobile-filters-button {
  position: fixed;
  bottom: 24px;
  left: 24px;
  z-index: 10;
  display: block;
  min-height: 2.4rem;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  pointer-events: none;
}

.shop-mobile-filters-button__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 6rem;
  @include box(fit-content, 6rem);
  padding: 1.8rem;
  background: var(--gsc-background-color-100);
  box-shadow: 0 0 1.6rem var(--gsc-text-color-10);
  border-radius: 3.2rem;
  cursor: pointer;

  @include font(var(--gsc-fz-12), var(--gsc-lh-5), var(--gsc-font-weight-bold));
}

.shop-mobile-filters-button__btn-label {
  max-width: 0;
  padding: 0;
  opacity: 0;
  transition-timing-function: ease-in-out;
  transition-duration: 0.3s;
  transition-property: opacity, max-width, padding;
}

.shop-mobile-filters-button[is-visible] {
  opacity: 1;
  pointer-events: all;
}

.shop-mobile-filters-button[is-extended]
  .shop-mobile-filters-button__btn-label {
  max-width: 20rem;
  padding: 0 0.6rem 0 0.8rem;
  opacity: 1;
}

@media (--md-screen) {
  .shop-mobile-filters-button {
    display: none;
  }
}
