.price-range__numbers {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin: 0 0 2rem;
}

.price-range__number {
  display: flex;
  width: 100%;
  padding: 0;
  color: var(--gsc-text-color-100);
  font-weight: var(--gsc-font-weight-bold);
  font-size: var(--gsc-fz-11);
  background-color: var(--gsc-background-color-100);
  border: none;
  outline: none;
}

.price-range__number + .price-range__number {
  text-align: right;
}

input[type='number']::-webkit-outer-spin-button,
input[type='number']::-webkit-inner-spin-button {
  appearance: none;
}

.price-range__slider {
  position: relative;
  height: 0.2rem;
  background: var(--gsc-border-color-100);
  border-radius: 5px;
}

.price-range__slider-wrap {
  padding: 0.4rem 0;
  cursor: pointer;
}

.price-range__slider-progress {
  position: absolute;
  right: 0%;
  left: 0%;
  height: 100%;
  background: var(--gsc-text-color-100);
  border-radius: 5px;
}

.price-range__inputs {
  position: relative;
  top: -2.6rem;
  @include box(100%, 1rem);
}

.price-range__input {
  position: absolute;
  @include box(100%, 1rem);
  margin: 0;
  background: none;
  cursor: pointer;
  appearance: none;
  pointer-events: none;
}

.price-range__input[targeted] {
  pointer-events: all;
}

.price-range__input::-webkit-slider-thumb {
  @include box(2.4rem, 2.4rem);
  background: var(--gsc-text-color-100);
  border-radius: 50%;
  cursor: pointer;
  appearance: none;
  pointer-events: auto;
  box-shadow: none;
}

.price-range__input::-moz-range-thumb {
  @include box(2.4rem, 2.4rem);
  background: var(--gsc-text-color-100);
  border: none;
  border-radius: 50%;
  appearance: none;
  pointer-events: auto;
  box-shadow: none;
}
