.product-variant-picker {
  display: block;
}

.product-variant-picker__popup-toggle {
  display: flex;
  align-items: center;
  margin-left: 0.8rem;
  column-gap: 0.4rem;
  cursor: pointer;
  transition: color 0.25s ease-in-out;

  @include font(
    var(--gsc-fz-12),
    var(--gsc-lh-5),
    var(--gsc-font-weight-normal)
  );
}

.product-variant-picker__popup-toggle span {
  border-bottom: 1px solid var(--gsc-text-color-0);
}

.product-variant-picker__form {
  display: none;
}

.product-variant-picker__list {
  position: relative;
  display: flex;
  flex-flow: column;
  row-gap: 2.4rem;
}

.product-variant-picker__pill-list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 1.2rem;
  row-gap: 1.2rem;
}

.product-variant-picker__option-label {
  display: flex;
  align-items: baseline;
  margin: 0 0 1.2rem;

  @include font(var(--gsc-fz-12), var(--gsc-lh-5), var(--gsc-font-weight-bold));
}

.product-variant-picker__option-active-color {
  margin-right: auto;
  margin-left: 0.8rem;
  font-weight: var(--gsc-font-weight-normal);
}

@media (--md-screen) {
  .product-variant-picker__popup-toggle:hover span {
    border-color: var(--gsc-text-color-100);
  }

  .product-variant-picker__list {
    row-gap: 3.2rem;
  }
}
