.shop {
  display: block;
}

.shop__container {
  overflow: hidden;
}

.shop__load-button-pagination {
  margin: 5.6rem 0 0;
}

.shop__classic-pagination {
  margin: 9.6rem 0 0;
}

.shop__content {
  display: flex;
  align-items: flex-start;
  padding-top: 2rem;
  padding-left: var(--gsc-container-padding);
  padding-right: var(--gsc-container-padding);
}

.container--with-aside-paddings .shop__content {
  padding-right: 0;
  padding-left: 0;
}

.shop__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 2.4rem;
  padding-bottom: 0.4rem;
  column-gap: 3.2rem;
}

.shop__header-right-panel {
  display: flex;
  align-items: center;
  margin: 0 0 0 auto;
  column-gap: 3.2rem;
}

.shop__header-left-panel {
  display: flex;
  align-items: center;
  column-gap: 2.4rem;
}

.shop__product-count {
  display: none;

  @include font(
    var(--gsc-fz-12),
    var(--gsc-lh-5),
    var(--gsc-font-weight-medium)
  );
}

.shop__column-switcher--desktop {
  display: none;
}

.shop__filters-reset {
  text-decoration: underline;
  cursor: pointer;

  @include font(var(--gsc-fz-12), var(--gsc-lh-5));
}

.shop__products {
  flex: 1;
  transition: width 250ms ease;
  max-width: 100%;
}

.shop__products-grid {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  column-gap: var(--gsc-space-between-cards);
  row-gap: var(--gsc-space-between-cards);
}

.shop__grid-item {
  width: calc(
    (100% - var(--gsc-space-between-cards) * (var(--gsc-mobile-columns) - 1)) /
      var(--gsc-mobile-columns)
  );
  transition: width 0.2s linear;
}

.shop__loading-overlay {
  position: absolute;
  z-index: 3;
}

.shop__products-stub {
  position: relative;
  display: flex;
  flex-flow: column;
  font-weight: var(--gsc-font-weight-bold);
}

.shop__products-grid-wrap {
  position: relative;
}

.shop__filters-btn {
  display: flex;
  align-items: center;
  cursor: pointer;

  @include font(var(--gsc-fz-12), var(--gsc-lh-5), var(--gsc-font-weight-bold));
}

.shop__filters-btn-wrapper {
  display: block;
  min-height: 2.4rem;
}

.shop__filters-btn-label {
  padding: 0 0 0 0.8rem;
}

.shop__filters-btn-wrapper--desktop {
  display: none;
}

@media (--md-screen) {
  .shop__column-switcher--desktop {
    display: flex;
  }

  .shop__column-switcher--mobile {
    display: none;
  }

  .shop__header {
    padding-bottom: 1.6rem;
  }

  .shop__content {
    display: flex;
    padding-top: 2.4rem;
  }

  .shop__product-count {
    display: block;
  }

  .shop__header-right-panel--reverse {
    margin: 0 auto 0 0;
  }

  .shop__header--reverse,
  .shop__header-left-panel--reverse,
  .shop__header-right-panel--reverse {
    flex-flow: row-reverse;
  }

  .shop__grid-item {
    width: calc(
      (100% - var(--gsc-space-between-cards) * (var(--gsc-desktop-columns) - 1)) /
        var(--gsc-desktop-columns)
    );
  }

  .shop__filters-btn-wrapper--desktop {
    display: flex;
  }

  .shop__filters-btn-wrapper--mobile {
    display: none;
  }

  .shop__filters-btn.is-opened .icon-filter__square {
    display: none;
  }

  .shop__filters-btn.is-opened .icon-filter__top-line {
    transform: rotate(-45deg) translate(-4px, 0);
  }

  .shop__filters-btn.is-opened .icon-filter__bottom-line {
    transform: rotate(45deg) translate(17px, -9px);
  }

  .shop__filters-btn .icon-filter__bottom-line,
  .shop__filters-btn .icon-filter__top-line {
    transition: transform 250ms ease;
  }

  .shop__filters-btn.is-opened:hover .icon-filter {
    transform: rotate(90deg);
  }
}
