.article__breadcrumbs-wrap {
  display: flex;
  justify-content: center;
  padding: 2.4rem 0;
}

.article__breadcrumbs-wrap--hidden-on-mobile {
  display: none;
}

.article__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.article__breadcrumbs {
  text-align: center;

  @include font(
    var(--gsc-fz-13),
    var(--gsc-lh-16),
    var(--gsc-font-weight-medium)
  );
}

.article__breadcrumbs-divider {
  margin: 0 1.2rem;
}

.article__breadcrumbs-item {
  text-decoration: none;
}

.article {
  position: relative;
  z-index: 1;
  padding-top: 0;
  padding-bottom: 4.8rem;
}

.article__blocks {
  display: flex;
  flex-flow: column;
  row-gap: 4rem;
}

.article__title {
  margin: 0;
}

.article__heading-share {
  margin: 4rem 0 0;
}

.article-meta {
  margin: 0 0 1.6rem;

  @include font(
    var(--gsc-fz-12),
    var(--gsc-lh-5),
    var(--gsc-font-weight-normal)
  );
}

.article-meta__item + .article-meta__item::before {
  display: inline-block;
  margin: 0 0.5rem 0 0.4rem;
  font-size: 1.6rem;
  content: '\2022';
}

.article__content {
  padding: 0 0 1.6rem;
}

.article__content p {
  font-weight: var(--gsc-font-weight-normal);
}

.article__content ul,
.article__content ol {
  margin: 0;
}

.article__content ol + *,
.article__content ul + *,
.article__content * + ol,
.article__content * + ul {
  margin-top: 2.4rem;
}

.article__content ol,
.article__content ul {
  padding-left: 2.8rem;
}

.article__content blockquote {
  position: relative;
  z-index: 1;
  padding: 0 0 0 4rem;
}

.article__content blockquote > p {
  color: var(--gsc-text-color-100);
  font-style: italic;

  @include font(var(--gsc-fz-5), var(--gsc-lh-2), var(--gsc-font-weight-bold));
}

.article__content blockquote::before {
  position: absolute;
  top: -2rem;
  left: 3rem;
  z-index: -1;

  @include box(7.2rem, 5.5rem);

  color: var(--gsc-foreground-color-100);
  font-size: 15rem;
  line-height: 12rem;
  background: var(--gsc-blockquote-icon) no-repeat center/cover;
  content: '';
}

.article__content a {
  position: relative;
  z-index: 1;
  color: var(--gsc-text-color-100);
}

.article__image-wrap {
  margin: 0 calc(var(--gsc-container-padding) * -1);
  border-radius: var(--gsc-base-radii, 0);
}

.round-style .article__image-wrap {
  margin: 0;
}

.article__background {
  position: absolute;
  top: var(--gsc-section-spacing-top, 0);
  left: 0;
  z-index: -1;
  display: none;

  @include box(100%, 84rem);

  padding: 0;
}

.article__background.container--with-aside-paddings {
  padding-right: var(--gsc-container-padding);
  padding-left: var(--gsc-container-padding);
}

.article__background-inner {
  position: relative;
  z-index: -1;

  @include box(100%, 100%);
}

.article__background-horizontal-square {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 50%;
  background-color: var(--gsc-foreground-color-100);
  border-radius: var(--gsc-base-radii, 0);
}

.article__background-vertical-square {
  position: absolute;
  top: 0;
  right: 0;

  @include box(50%, 100%);

  background-color: var(--gsc-foreground-color-100);
  border-radius: var(--gsc-base-radii, 0);
}

.article-footer-panel__share-btn {
  color: var(--gsc-text-color-80);
}

.article-footer-panel {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: space-between;
  margin: 0 0 5.6rem;
  padding: 1.6rem;
  column-gap: 0.8rem;
  background: var(--gsc-foreground-color-100);
  border-radius: var(--gsc-article-footer-radii, 0);
  row-gap: 1.6rem;
}

.article-footer-panel__article-meta {
  margin: 0;
}

.article-footer-posts {
  display: grid;
  grid-template-areas: 'prev-post next-post';
  grid-template-columns: repeat(2, 1fr);
}

.article-footer-posts_post {
  max-width: 28rem;
}

.article-footer-posts_post--prev {
  grid-area: prev-post;
  margin-right: auto;
  text-align: left;
}

.article-footer-posts_post--next {
  grid-area: next-post;
  margin-left: auto;
  text-align: right;
}

.article-footer-posts__post-link {
  margin: 0 0 0.8rem;
  text-transform: uppercase;
}

.article-footer-posts__post-title {
  display: none;

  @include font(
    var(--gsc-fz-12),
    var(--gsc-lh-5),
    var(--gsc-font-weight-medium)
  );
}

.article-comments__header {
  padding: 0 0 3.2rem;
  border-bottom: 1px solid var(--gsc-border-color-100);
}

.article-comments__comment-form,
.article-comments {
  margin: 8rem 0 0;
}

.article-comments__body-wrapper {
  position: relative;
}

.article-comments__overlay {
  position: absolute;
}

.article-comments__pagination {
  margin: 4.8rem 0 0;
}

.article__content img {
  display: block;
  margin: 4.8rem 0;
}

.article-tags__container {
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.2s ease-in-out;
  position: relative;
}

.article-tags__component {
  width: 100%;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}

.article-tags__component[visible] {
  opacity: 1;
}

.article-tags__tags {
  display: flex;
  flex-wrap: wrap;
  column-gap: 0.8rem;
  row-gap: 0.8rem;
}

.article-tags__tags--with-collapse {
  flex-wrap: nowrap;
}

.article-tags__tags--with-extra-rounded-tags {
  padding: 1.6rem;
}

.article-tags__tag {
  border-radius: var(--gsc-badge-radii, 0);
  color: var(--gsc-black-color-100);
  background: var(--gsc-foreground-color-100);
  backdrop-filter: blur(4px);

  @include font(
    var(--gsc-article-card-badge-font-size),
    var(--gsc-article-card-badge-line-height),
    var(--gsc-article-card-badge-font-weight)
  );

  z-index: 2;
  min-width: fit-content;
}

.article-tags__tag[tag-hidden] {
  position: absolute;
  opacity: 0;
  pointer-events: none;
  width: max-content;
}

.article-tags__tag > a {
  display: block;
  text-decoration: none;
  padding: 0.6rem 0.8rem;
  font-weight: 500;
}

.article-tags__dropdown {
  width: 250px;
}

.article-tags__dropdown-body {
  display: flex;
  flex-wrap: wrap;
  padding: 1.2rem;
  column-gap: 0.8rem;
  row-gap: 0.8rem;
  background: white;
}

@media (--md-screen) {
  .article__heading-share {
    margin: 4.8rem 0 0;
  }

  .article__breadcrumbs-wrap--hidden-on-mobile {
    display: flex;
  }

  .article__breadcrumbs-wrap {
    padding: 3.2rem 0;
  }

  .article__breadcrumbs-item:hover {
    text-decoration: underline;
  }

  .article__content {
    padding: 0 0 4.8rem;
  }

  .article__content blockquote {
    margin: 6.4rem 0;
    padding: 0 0 0 7.6rem;
  }

  .article__image-wrap {
    margin: 0;
  }

  .article__image-container.container--with-aside-paddings {
    padding-right: calc(var(--gsc-container-padding) * 2);
    padding-left: calc(var(--gsc-container-padding) * 2);
  }

  .article-comments__comment-form,
  .article-comments {
    margin: 9.6rem 0 0;
  }

  .article__blocks {
    row-gap: 4.8rem;
  }

  .article-meta {
    margin: 0 0 2.4rem;
  }

  .article-footer-panel .article-meta {
    margin: 0;
  }

  .article__background {
    display: block;
  }

  .article-footer-posts__post-title {
    display: block;
  }

  .article-footer-panel {
    flex-flow: row;
    margin: 0 0 4rem;
    padding: 1.2rem 1.6rem;
  }
}
