.product-variant-picker__image-swatches {
  display: grid;
  grid-template-columns: repeat(
    auto-fit,
    var(--gsc-product-section-swatch-width)
  );
  gap: 1.2rem;
}

.product-variant-picker__shape-swatches {
  display: grid;
  grid-template-columns: repeat(
    auto-fit,
    var(--gsc-product-section-swatch-width)
  );
  gap: 1.2rem;
}
