.instagram {
  overflow: hidden;
  text-align: center;
}

.instagram__text {
  display: inline-block;
}

.instagram__image-wrapper {
  z-index: 0; /* for work overflow:hidden + border-radius for child on iphone https://bugs.webkit.org/show_bug.cgi?id=98538 */
  overflow: hidden;
  border-radius: var(--gsc-base-radii, 0);
}

.instagram__image {
  position: absolute;
  top: 0;
  left: 0;
  @include box(100%, 100%);
}

.instagram__btn {
  width: 100%;
}

.instagram__icon {
  @include box(3.6rem, 3.6rem);
}

.instagram__item {
  position: relative;
  width: 100%;
}

.instagram__link-overlay {
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--gsc-white-color-100);
  background: var(--gsc-black-color-30);
  opacity: 0;
  transition: opacity 250ms ease-in-out;
}

.instagram__link-overlay:focus-visible {
  opacity: 1;
}

.instagram__content > * + * {
  margin: 1.6rem 0 0;
}

.instagram__content {
  margin: 0 auto;
}

.instagram__content--with-left-alignment {
  text-align: left;
}

.instagram__content--with-center-alignment {
  text-align: center;
}

.instagram__content--with-right-alignment {
  text-align: right;
}

.instagram__container > * + * {
  margin: 4rem 0 0;
}

@media (--md-screen) {
  .instagram__content > * + * {
    margin: 2rem 0 0;
  }

  .instagram__container > * + * {
    margin: 4rem 0 0;
  }

  .instagram__btn {
    width: auto;
  }

  .instagram__item:hover .instagram__link-overlay {
    opacity: 1;
  }
}
