.password-modal {
  position: fixed;
  inset: 0;
  z-index: var(--gsc-modal-zIndex);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  transform: translateY(-100%);
  opacity: 0;
  transition-timing-function: ease;
  transition-duration: 0.25s;
  transition-property: opacity, transform;
  pointer-events: none;
  will-change: transform;
}

.password-modal.is-opened {
  transform: translateY(0%);
  opacity: 1;
  pointer-events: all;
}

.password-modal__close {
  position: absolute;
  top: 2rem;
  right: 2rem;
  z-index: 2;
  @include box(3.2rem, 3.2rem);
  color: var(--gsc-text-color-100);
  cursor: pointer;
}

.password-modal__body {
  position: relative;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  @include box(100%, 100%);
  margin: 0 auto;
  padding: 4rem;
  overflow: auto;
  overflow-x: hidden;
  overflow-y: auto;
  background-color: var(--gsc-background-color-100);
  outline: none;
}

.password-modal__login-form {
  width: 40rem;
  max-width: 100%;
  margin: 1.6rem 0;
}

.password-modal__login-form-auth-link {
  @include font(
    calc(var(--gsc-body-font-scale) * var(--gsc-fz-11)),
    var(--gsc-lh-4)
  );
}

.password-modal__login-form-field {
  position: relative;
}

.password-modal__login-form-btn {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  @include box(5.6rem, 5.6rem);
  padding: 0;
}

.password-modal__login-form-btn-arrow-icon {
  @include box(2rem, 2rem);
}
