.comment {
  display: flex;
  flex-flow: column;
  row-gap: 1.2rem;
  padding: 2.4rem 0;
  font-weight: var(--gsc-font-weight-medium);
  border-bottom: 1px solid var(--gsc-border-color-100);
}

.comment__author {
  font-weight: var(--gsc-font-weight-bold);
}

.comment__date {
  color: var(--gsc-text-color-35);

  @include font(var(--gsc-fz-12), var(--gsc-lh-5));
}
