.zoom-cursor {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--gsc-zoom-cursor-zIndex);
  @include box(5.6rem, 5.6rem);
  opacity: 0;
  transition: opacity 0.25s ease-in-out;
  pointer-events: none;
}

.zoom-cursor::before {
  position: absolute;
  top: 0;
  left: 0;
  @include box(5.6rem, 5.6rem);
  background: var(--gsc-background-color-70);
  border-radius: 50%;
  box-shadow: 0 0.4rem 1.6rem rgb(0, 0, 0, 0.08);
  transform-origin: center center;
  backdrop-filter: blur(4px);
  transition: transform 0.3s ease-in-out;
  content: '';
}

.zoom-cursor__icon {
  position: relative;
  z-index: 1;
  @include box(2.4rem, 2.4rem);
}

@media (--lg-screen) {
  [data-zoom-cursor-target] {
    cursor: none;
  }

  .zoom-cursor.is-visible {
    opacity: 1;
  }

  .zoom-cursor[zoomed] .y-line {
    display: none;
  }
}
