.footer__container {
  overflow: hidden;
  border-radius: var(--gsc-base-radii, 0);
}

.footer__container--bordered {
  border-top: 1px solid var(--gsc-border-color-100);
}

.footer__card-grid {
  padding: 5rem 0 5.6rem;
}

.footer__block {
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  overflow: hidden;
}

.footer__image-wrap {
  display: block;
  width: var(--gsc-image-width);
  overflow: hidden;
  border-radius: var(--gsc-base-radii, 0);
}

.footer__image {
  position: absolute;
  top: 0;
  left: 0;
}

.footer__menu {
  display: flex;
  flex-flow: column;
  row-gap: 1.2rem;
}

.footer__title {
  margin: 0 0 2rem;
}

.footer__text + .footer__social-list {
  margin-top: 1.6rem;
}

.footer__social-list {
  margin-left: -1rem;
}

.footer__utils {
  display: flex;
  flex-flow: column;
  padding: 3rem 0;
}

.footer__utils--bordered {
  border-top: 1px solid var(--gsc-border-color-100);
}

.footer__newsletter {
  width: 100%;
}

.footer__text + .footer__newsletter {
  margin: 1.2rem 0 0;
}

.footer__logo {
  margin: 0 0 3.2rem;
}

.footer__logo .logo-img {
  max-width: 100%;
}

.footer__settings {
  display: flex;
  column-gap: 2.4rem;
}

.footer__bottom-bar {
  display: flex;
  flex-wrap: wrap;
  column-gap: 2.4rem;
  row-gap: 2.4rem;
}

.footer__bottom-bar-item {
  @include font(var(--gsc-fz-13), var(--gsc-lh-7));
}

.footer__payment-icons {
  display: flex;
  flex-wrap: wrap;
  column-gap: 0.8rem;
  row-gap: 0.8rem;
}

.footer__extra {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 2.4rem;
  row-gap: 2.4rem;
  margin: 2.2rem 0 0;
}

.footer__copyright {
  @include font(var(--gsc-fz-13), var(--gsc-lh-7));
}

.footer__utils > * + .footer__bottom-bar {
  margin: 2.8rem 0 0;
}

.footer__utils > * + .footer__copyright {
  margin: 3.2rem 0 0;
}

.footer .localization-dropdown {
  max-width: calc(100% - 4rem);
  margin: 0 0 0 -2rem;
}

.footer__policies {
  display: flex;
  flex-wrap: wrap;
  column-gap: 2.4rem;
  row-gap: 2.4rem;
  padding: 0 0 3rem;
}

.footer__policies-item {
  @include font(var(--gsc-fz-13), var(--gsc-lh-7));
}

@media (--md-screen) {
  .footer .localization-dropdown {
    max-width: unset;
    margin: 0;
  }

  .footer__card-grid {
    padding: 8rem 0 6.4rem;
  }

  .round-style .footer__card-grid {
    padding: 5.6rem 0;
  }

  .footer__container {
    --gsc-container-padding: 4rem;
  }

  .footer__utils {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 2.8rem;
  }

  .footer__settings {
    grid-area: settings;
    align-items: center;
    column-gap: 1.6rem;
  }

  .footer__bottom-bar {
    flex: 1;
    grid-area: bottom-bar;
  }

  .footer__utils > * + .footer__bottom-bar {
    margin: 0 0.8rem 0 0;
    min-width: fit-content;
  }

  .footer__utils > * + .footer__copyright {
    margin: 0;
  }

  .footer__payment-icons {
    grid-area: payment-icons;
    align-items: center;
  }

  .footer__extra {
    display: flex;
    column-gap: 2.8rem;
    row-gap: 2.8rem;
    margin: 0 0 0 auto;
  }

  .footer__copyright {
    grid-area: copyright;
  }

  .footer__selector,
  .footer__settings .dropdown,
  .footer__settings .localization-dropdown-toggle,
  .footer__settings form {
    height: 100%;
  }

  .footer__settings .localization-dropdown-toggle {
    padding: 0;
  }

  .footer__dropdown-menu {
    width: 30rem;
  }
}
