.progress-bar {
  position: relative;
  display: block;
  @include box(100%, var(--gsc-progress-bar-width));
  overflow: hidden;

  --gsc-progress-bar-width: 0.15rem;
}

.progress-bar--with-background {
  background: var(--gsc-text-color-10);
}

.progress-bar--vertical {
  @include box(var(--gsc-progress-bar-width), 100%);
}

.progress-bar__indicator {
  position: absolute;
  top: 0;
  left: 0;
  @include box(100%, 100%);
  background: var(--gsc-text-color-100);
}

.progress-bar__indicator {
  left: -100%;
}

.progress-bar__indicator--vertical {
  top: -100%;
  left: 0;
}
